function drawArea(testcase, eventRef, width, height, item) {
  let slide = width / 10;

  eventRef.current.style.position = 'absolute';

  testcase.beginPath();
  testcase.moveTo(slide * item.value - 10, 5); // starting point (left ,top)
  // testcase.lineTo(slide * item.value, 10 ); // bottom point (left ,top)
  // testcase.lineTo(slide * item.value ,0)

  testcase.lineTo(slide * item.value + 10, 5);
  testcase.lineTo(slide * item.value, 20);
  testcase.lineTo(slide * item.value - 10, 5);
  testcase.fill();
  testcase.lineTo(slide * item.value, 20);
  testcase.lineTo(slide * item.value, height);

  //   testcase.fill();
  testcase.fillStyle = '#9999ff';
  testcase.strokeStyle = '#ccccffe6';

  testcase.stroke();

  let left = relativepos.left;

  arr.push({
    left: left,
    start: (relativepos.width / 10) * item.value + left,
    //eventName: item.eventName,
    tippos: (relativepos.width / 10) * item.value,
    ...item,
  });
}

function draw(testcase, tipRef, eventRef, w, h, data) {
  //const canvas = testcase.canvas;
  testcase.fillStyle = '#9999ff';

  arr = [];
  columnPos = [];
  //  testcase.fillStyle = '#CEF2FE';

  data.map(item => {
    drawArea(testcase, eventRef, w, h, item);
  });
}
function hoverColumn(x) {
  let hoveredCol = arr.filter(
    item => item.start - 1 <= x && x <= item.start + 1,
  )[0];
  //console.log(hoveredCol);
  return hoveredCol;
}

function EventPointerHar({ data, isLeft }) {
  const eventRef = useRef();
  const tooltipRef = useRef();
  const lineRef = useRef();
  useEffect(() => {
    let eventElement = eventRef.current.getContext('2d');

    if (eventRef.current) {
      eventRef.current.position = 'absolute';
      relativepos = eventRef.current.getBoundingClientRect();
      draw(
        eventElement,
        tooltipRef,
        eventRef,
        window.innerWidth,
        window.innerHeight,
        data,
      );
      // eventElement.fillStyle = 'transparent';

      //    data.map(item => {
      //     drawArea(eventElement, window.innerWidth, 15, item);
      // })
      const handleResize = () => {
        relativepos = eventRef.current.getBoundingClientRect();
        //  //console.log(testcase);
        eventElement.canvas.height = window.innerHeight;
        eventElement.canvas.width = window.innerWidth;
        draw(
          eventElement,
          tooltipRef,
          eventRef,
          window.innerWidth,
          window.innerHeight,
          data,
        );
      };
      handleResize();
      const mouseHandler = e => {
        if (eventRef.current) {
          const tooltip = tooltipRef.current.getContext('2d');
          tooltip.fillStyle = '#ffffff'; //'#6CB2D8' ;// '#290b08cc';
          //      tooltip.font = '12px montserrat';
          tooltipRef.current.position = 'absolute';

          const val = hoverColumn(e.clientX);

          if (val && e.clientX < 1300) {
            //   console.log('match');
            tooltipRef.current.style.background = '#ffffff'; //'#6CB2D8';  // '#000000cc';
            //  tooltipRef.current.style.color = 'black';              //'#ffffff';
            tooltipRef.current.style.fontFamily = 'montserrat';
            tooltipRef.current.style.fontWeight = 500;
            tooltipRef.current.style.zIndex = 1000;
            tooltipRef.current.style.borderRadius = '5px';
            tooltipRef.current.style.bgColor = '#ffffff'; // '#6CB2D8'

            tooltipRef.current.style.fontSize = '12px';
            tooltipRef.current.style.display = 'block';
            tooltipRef.current.style.right = '10px';
            tooltipRef.current.style.top = '18px';
            tooltipRef.current.style.zIndex = 2;

            tooltipRef.current.style.left = val.tippos - 110 + 'px';

            tooltip.save();
            ////console.log(tooltip);
            tooltip.clearRect(0, 0, 220, 110);
            tooltip.fillRect(0, 0, 220, 110);
            tooltip.fillStyle = 'black'; //'white';
            tooltip.font = 'normal 12px ';
            tooltip.fillText('event : ' + val.eventName, 5, 12);
            tooltip.fillText(val.testCase, 5, 25);

            //tooltip.font = '12px'
            tooltip.restore();
          } else {
            tooltipRef.current.style.display = 'none';
            // console.log('not match');
          }
        }
      };
      eventRef.current.addEventListener('mousemove', mouseHandler);

      window.addEventListener('resize', handleResize);
      return () => {
        window.removeEventListener('mousemove', mouseHandler);
        window.removeEventListener('resize', handleResize);
      };
    }
  }, [isLeft]);

  return (
    <Flex height="10px" position="relative">
      <Flex
        position="absolute"
        top="-2px"
        h="35px"
        width="100%"
        zIndex="1"
        padding="0px 40px 0px 10px"
        paddingLeft={isLeft ? '215px' : '10px'}
      >
        <Flex position="relative" width="100%">
          <canvas
            ref={eventRef}
            id="testcase"
            style={{
              width: '100%',
              height: '450px',
              zIndex: 1,
              top: '0px',
              fontSize: '12px',
              position: 'absolute',
              // background : 'red'
            }}
          ></canvas>
          <canvas
            ref={tooltipRef}
            height={32}
            width={210}
            style={{
              position: 'absolute',
              background: 'transparent',
              display: 'none',
              fontSize: '12px',
              boxShadow: '0px 3px 6px #00000029',
              color: 'black',
              fontFamily: 'montserrat',
              zIndex: 1000,
            }}
          ></canvas>
          {/* <canvas ref={lineRef} style={{ position: 'absolute' , background: 'transparent', width: '100%', height: '200px',zIndex: '2' }} ></canvas> */}
        </Flex>
      </Flex>
    </Flex>
  );
}

export default EventPointerHar;
