// @ts-nocheck
import AccordionComponent from 'app/components/AccordionComponent';
import NoContent from 'app/components/NoContent';
import React from 'react';
import SessionidTable from '../components/SessionidTable';

function DisplayTable({ testSessionData, tabType }) {
  return (
    <>
      {Object.keys(testSessionData).length !== 0 ? (
        <>
       <AccordionComponent  isDefault={true} title="">
       <SessionidTable
          testSessionData={testSessionData}
          tabType={tabType}
        ></SessionidTable>
       </AccordionComponent>
      </>
      ) : (
        <NoContent message="No Content Available"></NoContent>
      )}
    </>
  );
}

export default DisplayTable;
