// @ts-nocheck
import React from 'react';
import {
  Flex,
  Table,
  TableCaption,
  Th,
  Tr,
  Thead,
  Td,
  Tbody,
  Img,
} from '@chakra-ui/react';
import loadingImg from '../../styles/Assets/load-loading.gif';
import { selectTestSessionData } from 'app/pages/TestSession/slice/selectors';
import { useSelector } from 'react-redux';
import { dateTimeConverter } from 'utils/gernricfunction';
function SummaryTableComp({ tableHeader, summaryTableData, isLoading, title }) {
  // console.log(summaryTableData);
  const testSessionData = useSelector(selectTestSessionData);

  const offset =
    Object.keys(testSessionData).length !== 0 &&
    testSessionData.testStartTime.split('+')[1];

  return (
    <>
      {summaryTableData && (
        <Table
          variant="simple"
          size="sm"
          width="100%"
          backgroundColor="white"
          fontFamily="Montserrat"
          fontWeight="500"
        >
          {/* <TableCaption >Test CaseSummary</TableCaption> */}
          <Thead backgroundColor="white3" textTransform="capitalize">
            <Tr>
              {tableHeader.map((heading, index) => (
                <Th
                  key={index}
                  fontFamily="Montserrat"
                  color="grey1"
                  fontWeight="500"
                  textTransform="capitalize"
                  padding="6px"
                >
                  {heading}
                </Th>
              ))}
            </Tr>
          </Thead>
          {title === 'Experience KPIs' && (
            <Tbody>
              {summaryTableData.map((item, index) => (
                <Tr textTransform="none" key={index} borderBottomColor="grey2">
                  <Td textTransform="none" padding="5px">
                    {item.testCase}
                  </Td>
                  <Td textTransform="none" padding="5px">
                    {item.kpiName}
                  </Td>
                  {/* <Td padding="5px">{item.endTime}</Td> */}
                  <Td textTransform="none" padding="5px">
                    {item.value}
                  </Td>
                </Tr>
              ))}
            </Tbody>
          )}

          {title === 'Test Cases Summary' && (
            <Tbody>
              {summaryTableData.map((item, index) => (
                <Tr key={index} textTransform="none" borderBottomColor="grey2">
                  <Td padding="6px">{item.testCaseName}</Td>
                  <Td padding="6px">{dateTimeConverter(item.startTime)}</Td>
                  <Td padding="6px">{dateTimeConverter(item.endTime)}</Td>
                  <Td minWidth="100px" padding="6px">
                    {item.status}
                  </Td>
                </Tr>
              ))}
            </Tbody>
          )}

          {title === 'Event Summary' && (
            <Tbody>
              {summaryTableData.map((item, index) => (
                <Tr borderBottomColor="grey2" key={index}>
                  <Td textTransform="none" padding="6px">
                    {item.eventName}
                  </Td>
                  <Td textTransform="none" padding="6px">
                    {' '}
                    {dateTimeConverter(item.time)}
                  </Td>
                  <Td textTransform="none" padding="6px">
                    {item.testCase}
                  </Td>
                  {/* <Td padding="6px">{item.kpiName}</Td> */}
                </Tr>
              ))}
            </Tbody>
          )}
        </Table>
      )}
    </>
  );
}

export default SummaryTableComp;
