const getTwoDigitFormat = val => {
  if (val <= 9) {
    return '0' + val;
  }
  return val;
};
const getThreeDigitFormat = val => {
  // if (val <= 99) {
  //   return  val.toFixed(0);
  // }
  return (val / 1000).toFixed(0);
};

const makeLabels = time => {
  // console.log(time/10)
  //time = 74000;
  let label = [];
  let labelInfo = [];
  let unit = 'ms';
  // let timeDuration = time
  if (time > 1000) {
    time = time / 1000;
    unit = 's';
    if (time > 60) {
      time = time / 60;

      unit = 'm';
      if (time > 60) {
        time = time / 60;
        unit = 'h';
      }
    }
  }
  // console.log(time + unit);
  let interval = time / 10;
  //console.log(time+unit);

  for (let i = 0; i <= 10; i++) {
    let unit2 = 'ms';
    const perfect = Math.floor(interval * i);

    if (unit === 'm') {
      //console.log( Math.floor(((interval*i)-perfect)*60));

      unit2 = 's';
      label.push(
        getTwoDigitFormat(perfect) +
          ':' +
          getTwoDigitFormat(Math.floor((interval * i - perfect) * 60)) +
          unit2,
      );
      labelInfo.push(
        getTwoDigitFormat(perfect) +
          unit +
          ':' +
          getTwoDigitFormat(Math.floor((interval * i - perfect) * 60)) +
          unit2,
      );
    } else if (unit === 'h') {
      //   console.log( Math.floor(((interval*i)-perfect)*60));
      unit2 = 's';
      label.push(
        getTwoDigitFormat(perfect) +
          ':' +
          getTwoDigitFormat(Math.floor((interval * i - perfect) * 60)) +
          unit2,
      );

      labelInfo.push(
        getTwoDigitFormat(perfect) +
          unit +
          ':' +
          getTwoDigitFormat(Math.floor((interval * i - perfect) * 60)) +
          unit2,
      );
    } else if (unit === 's') {
      unit2 = 'ms';
      label.push(
        getTwoDigitFormat(perfect) +
          ':' +
          getThreeDigitFormat(Math.floor((interval * i - perfect) * 1000)) +
          unit2,
      );
      labelInfo.push(
        getTwoDigitFormat(perfect) +
          unit +
          ':' +
          getThreeDigitFormat(Math.floor((interval * i - perfect) * 1000)) +
          unit2,
      );
    } else {
      label.push((interval * i).toFixed(0) + unit);
      labelInfo.push((interval * i).toFixed(0) + unit);
    }

    // console.log(perfect);
  }
  //console.log(label)

  return labelInfo;
};

function SliderAxisHar({ children, duration, startTime, endTime, isLeft }) {
  const [labels, setLabels] = useState([]);
  const [isEnableEvent, setEventEnable] = useState(false);
  const summaryTableData = useSelector(selectsummaryTableData);
  // console.log(duration / 10000);
  const ticksize = duration / 10000;
  // console.log(new Date(startTime));
  // console.log(new Date(endTime));
  //   console.log(summaryTableData);
  let data = [];
  // eventdata.map((value, index) => {
  summaryTableData.map((value, index) => {
    //console.log(value);
    data.push({
      value:
        (new Date(value.time).valueOf() - new Date(startTime).valueOf()) /
        1000 /
        ticksize,
      id: index,
      ...value,
    });

    // console.log(
    //   (new Date(value.time).valueOf() - new Date(startTime).valueOf()) / 1000,
    // );
    // console.log(new Date(value.time))
  });
  // console.log(data);

  useEffect(() => {
    setLabels(makeLabels(duration));
  }, [duration]);
  // console.log(labels);
  return (
    <Flex
      width="100%"
      flexDirection="column"
      padding="0px"
      margin="0px"
      fontFamily="Montserrat"
      fontWeight="500"
    >
      {summaryTableData.length > 0 && (
        <Flex padding="5px" paddingBottom="0px">
          <HStack alignContent="center">
            <Text fontSize="12px" textAlign="end" as="span">
              Enable Events
            </Text>
            <Switch
              isFocusable={false}
              size="sm"
              height="16px"
              _focus={{ boxShadow: '0px 0px 0px white' }}
              margin="0px"
              padding="0px"
              isChecked={isEnableEvent}
              onChange={e => setEventEnable(e.target.checked)}
              marginTop="5px"
            />
          </HStack>
        </Flex>
      )}

      <Flex
        fontFamily="Montserrat"
        fontWeight="300"
        height="25px"
        marginTop="10px"
        backgroundColor="#DEDEDE"
        justifyContent="space-between"
        borderRadius="2px"
        width="100%"
        color="black"
        paddingLeft={isLeft ? '212px' : '10px'}
        //paddingLeft="10px"
        alignContent="flex-end"
      >
        {labels.map(item => (
          <Flex
            padding="1px"
            fontSize="8px"
            whiteSpace="nowrap"
            textAlign="start"
            as="span"
            //width="45px"
            boxSize={`calc(100% / 10)`}
          >
            | {item}
          </Flex>
        ))}
      </Flex>
      {!isEnableEvent && <Flex h="10px"></Flex>}
      {isEnableEvent && (
        <EventPointerHar isLeft={isLeft} data={data}></EventPointerHar>
      )}
      {children}
    </Flex>
  );
}

export default SliderAxisHar;
