// @ts-nocheck
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import {
  selectTestSessionData,
  selectLoading,
  selectError,
  selectAbortTestResponse,
  selectAbortTestError,
  selectLoadingAbort,
} from './slice/selectors';
import { TestSessionErrorType } from './slice/types';
import { useTestSessionSlice } from './slice';
import DisplayTable from './Features/DisplayTable';
import SubContainer from 'app/components/SubContainer';
import SubNavbar from './components/SubNavbar';
import SubTabWrapper from './Features/SubTabWrapper';
import TestCases from '../TestCases';
import { Flex } from '@chakra-ui/layout';
import ExperienceKpi from '../ExperienceKpi';
import HarAnalysis from '../HarAnalysis';
import loadingimage from '../../../styles/Assets/load-loading.gif';
import { Img } from '@chakra-ui/image';

import SubheaderSessionId from './components/SubheaderSessionId';
import {
  selectSessionId,
  selecttestSessionIdDetails,
} from '../TestAnalytics/testAnalyticsSlice/selectors';
import SubTabsComponent from './components/SubTabsComponent';
import { generatePath, useNavigate, useParams } from 'react-router-dom';
import { DownloadLog } from '../DownLoadLog/Loadable';
import { AppProfiling } from '../AppProfiling/Loadable';
import LoadingComponent from 'app/components/LoadingComponent';
import NoContent from 'app/components/NoContent';
//import features from '../../../features.json';
import { useToast } from '@chakra-ui/react';
import LiveLog from '../LiveLog';
import Testcasenetweb from '../Testcasesnetweb';
import { selectFeatureFlagConfigData } from 'Authentication/slice/selectors';
import ResizablePanel from './components/ResizablePanel';

export function TestSession() {
  const features = useSelector(selectFeatureFlagConfigData);
  const toast = useToast();
  const { actions } = useTestSessionSlice();
  // const [tab, setTab] = useState('app')
  const { testId } = useParams();
  const testSessionData = useSelector(selectTestSessionData);
  const error = useSelector(selectError);
  const aportTestResponseData = useSelector(selectAbortTestResponse);
  const abortTestError = useSelector(selectAbortTestError);
  const isLoading = useSelector(selectLoading);
  const loadingAboertResponse = useSelector(selectLoadingAbort);
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(actions.loadTestSessionData(testId));

    return () => {
      dispatch(actions.testSessionDataLoaded({}));
    };
  }, [testId]);

  useEffect(() => {
    if (
      aportTestResponseData.hasOwnProperty('message') &&
      abortTestError == null
    ) {
      if (
        aportTestResponseData.hasOwnProperty('message') &&
        aportTestResponseData.message === 'Success'
      ) {
        //   localStorage.setItem('testStatus', 'Aborted');
        toast({
          title: 'Aborted Successfully',
          status: 'success',
          isClosable: true,
          position: 'bottom-right',
        });
        dispatch(actions.loadTestSessionData(testId));
      } else {
        toast({
          title: aportTestResponseData.message,
          status: 'error',
          isClosable: true,
          position: 'bottom-right',
        });
      }
      dispatch(actions.abortedTestResponse({}));
    }
    if (abortTestError === 1) {
      toast({
        title: 'Abort action could not be completed.',
        status: 'error',
        isClosable: true,
        position: 'bottom-right',
      });
      dispatch(actions.abortTestError(null));
    }
  }, [aportTestResponseData, abortTestError]);

  const harEnable =
    features.features.enable[
      'test-analytics-display-test-execution-har-analytics'
    ];
  const appProfilingEnable =
    features.features.enable[
      'test-analytics-display-test-execution-app-profiling'
    ];

  const tab = //'lr-automation';
    testSessionData.hasOwnProperty('extra') &&
    testSessionData.extra.hasOwnProperty('testType') &&
    testSessionData.extra.testType;
  const deviceType = //'tv'
    testSessionData.hasOwnProperty('extra') &&
    testSessionData.extra.hasOwnProperty('deviceType') &&
    testSessionData.extra.deviceType;
  const orientationView = //'landscape'
    testSessionData.hasOwnProperty('extra') &&
    testSessionData.extra.hasOwnProperty('orientation') &&
    testSessionData.extra.testType.orientation;

  const getUserEmail =
    testSessionData.hasOwnProperty('uuid') &&
    testSessionData.uuid.hasOwnProperty('userEmail') &&
    testSessionData.uuid.userEmail;

  const deviceId =
    testSessionData.hasOwnProperty('deviceSerial') &&
    testSessionData.deviceSerial;
  const livelogflag =
    testSessionData.hasOwnProperty('testStatus') &&
    testSessionData.testStatus === 'Started';

  const listofComponents =
    tab === 'network'
      ? [
          <Testcasenetweb></Testcasenetweb>,
          <DownloadLog></DownloadLog>,
          <Flex>pcap</Flex>,
          <Flex>tableau</Flex>,
        ]
      : tab === 'web'
      ? [<Testcasenetweb></Testcasenetweb>, <DownloadLog></DownloadLog>]
      : tab === 'lr-automation' ||
        tab === 'lr-interact' ||
        tab === 'mobile-interact'
      ? [
          tab === 'lr-automation' && <TestCases></TestCases>,
          true && <DownloadLog></DownloadLog>,
          tab === 'lr-automation' && livelogflag && <LiveLog></LiveLog>,
        ]
      : [
          true && <TestCases></TestCases>,
          appProfilingEnable && <AppProfiling></AppProfiling>,
          harEnable && <HarAnalysis></HarAnalysis>,
          true && <DownloadLog></DownloadLog>,
          livelogflag && <LiveLog></LiveLog>,
        ];

  const listofTabs =
    tab === 'network'
      ? ['TEST CASES', 'DOWNLOADS', 'PCAP ANALYSIS', 'TABLEAU']
      : tab === 'web'
      ? ['TEST CASES', 'DOWNLOADS']
      : tab === 'lr-automation' ||
        tab === 'lr-interact' ||
        tab === 'mobile-interact'
      ? [
          tab === 'lr-automation' && 'TEST CASES',
          true && 'DOWNLOADS',
          tab === 'lr-automation' && livelogflag && 'LIVE LOG',
        ]
      : [
          true && 'TEST CASES',
          appProfilingEnable && 'APP PROFILING',
          harEnable && 'NETWORK',
          true && 'DOWNLOADS',
          livelogflag && 'LIVE LOG',
        ];

  const [currentTabId, setCurrentTabId] = useState('testcase');
  // document.getElementById("currentTabId").style.borderBottom ="3px solid blue";
  const tabHandler = e => {
    setCurrentTabId(e.target.id);
  };

  const abortTestHandler = () => {
    dispatch(actions.loadAbortTest(testId));
  };

  return (
    <>
      <SubheaderSessionId
        getUserEmail={getUserEmail}
        testId={testId}
        tabType={tab}
        status={testSessionData}
        abortTestHandler={abortTestHandler}
      ></SubheaderSessionId>
      <SubContainer>
        {isLoading || loadingAboertResponse ? (
          <LoadingComponent height="120px"></LoadingComponent>
        ) : (
          <>
            {error === null ? (
              <DisplayTable testSessionData={testSessionData}></DisplayTable>
            ) : (
              <NoContent
                message="Something Went wrong"
                height="120px"
              ></NoContent>
            )}
          </>
        )}
        <SubTabWrapper>
          <SubTabsComponent
            tabType={tab}
            deviceType={deviceType}
            orientationView={orientationView}
            isBorder={true}
            listofTabs={listofTabs}
            listofComponents={listofComponents}
          ></SubTabsComponent>
        </SubTabWrapper>
      </SubContainer>
    </>
  );
}

export const repoErrorText = (error: TestSessionErrorType) => {
  switch (error) {
    case TestSessionErrorType.RESPONSE_ERROR:
      return 'There is no such user ';
    default:
      return 'An error has occurred!';
  }
};
