import { createSelector } from '@reduxjs/toolkit';

import { RootState } from 'types';
import { initialState } from '.';

// First select the relevant part from the state
const selectSlice = (state: RootState) => state.testSession || initialState;

export const selectTestSessionData = createSelector(
  [selectSlice],
  state => state.testSessionData,
);

export const selectLoading = createSelector(
  [selectSlice],
  state => state.loading,
);

export const selectError = createSelector([selectSlice], state => state.error);
export const selectAbortTestResponse = createSelector(
  [selectSlice],
  state => state.abortTestResponse,
);
export const selectAbortTestError = createSelector(
  [selectSlice],
  state => state.errorTestAbort,
);
export const selectLoadingAbort = createSelector(
  [selectSlice],
  state => state.loadingAbort,
);
