import { createSelector } from '@reduxjs/toolkit';
import { RootState } from 'types';
import { initialState } from '.';

const selectSlice = (state: RootState) => state.harChart || initialState;

export const selectHarChartData = createSelector(
  [selectSlice], // state.testNotification
  state => state.HarEntiesData,
);
export const selectHarAnalysis = createSelector([selectSlice], state => state);
export const selectHarIssueSummaryEntiesData = createSelector(
  [selectSlice], // state.testNotification
  state => state.HarIssueSummaryEnties,
);
export const selectHarChartDetailData = createSelector(
  [selectSlice], // state.testNotification
  state => state.Hardata,
);

export const selectHarLoading = createSelector(
  [selectSlice],
  state => state.loading,
);
export const selectHarKpiData = createSelector(
  [selectSlice], // state.testNotification
  state => state.harKpiData,
);
export const selectHarUrl = createSelector(
  [selectSlice],
  state => state.harUrlData,
);

export const selectKpiHarLoading = createSelector(
  [selectSlice],
  state => state.loadingUrl,
);

export const selectHarKpiDataError = createSelector(
  [selectSlice],
  state => state.harKpiErrors,
);
export const selectHarChartDataError = createSelector(
  [selectSlice],
  state => state.errors,
);
export const  selectHarChart = createSelector([selectSlice], state => state)

