// @ts-nocheck
import { Box, Button, Flex, Icon, Text, Tooltip } from '@chakra-ui/react';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import React, { useState } from 'react';
import { MdContentCopy } from 'react-icons/md';
import { TiTick } from 'react-icons/ti';
import DownloadJson from './DownloadJson';

interface PropsType {
  text?: string | number;
  borderBottom?: string;
  date?: string | number;
  isDownload?: boolean;
  fileName?: string;
  isNotHover?: boolean;
  tooltipMessage?: string;
}

function CopyToClipBoardComp({
  text,
  borderBottom,
  data,
  isDownload,
  fileName,
  isNotHover,
  tooltipMessage,
}: propsType) {
  const [getText, setText] = useState(text);
  const [copyIcon, setCopyIcon] = useState(true);
  const setCopyIconHandler = () => {
    setCopyIcon(false);
    setTimeout(() => {
      setCopyIcon(true);
    }, 2000);
  };

  return (
    <Flex
      id="hoverTargetId"
      alignContent="center"
      borderBottom={borderBottom ? '1px solid ' : '0px solid'}
      borderBottomColor="white8"
      padding="1px 2px 1px 0px"
      width="100%"
    >
      <Flex width="8%" height="13px" alignContent="center" alignItems="center">
        {isDownload && (
          <Flex
            display="none"
            color="grey2"
            alignContent="center"
            marginTop="4px"
            sx={{
              '#hoverTargetId:hover &': {
                display: 'block',
              },
            }}
          >
            <DownloadJson fileName={fileName} data={data}></DownloadJson>
          </Flex>
        )}
        <Flex alignContent="center" justifyContent="center" alignItems="center">
          <CopyToClipboard text={text} onCopy={setCopyIconHandler}>
            <Flex
              alignContent="center"
              alignItems="center"
              flexDirection="row"
              display={isNotHover ? 'block' : 'none'}
              // padding="0px 2px"
              color="grey2"
              cursor="pointer"
              //  right="20"
              sx={{
                '#hoverTargetId:hover &': {
                  display: 'block',
                },
              }}
            >
              <Tooltip
                label={tooltipMessage}
                fontSize="10px"
                placement="auto-start"
              >
                <span>
                  {copyIcon ? (
                    <Icon
                      cursor="pointer"
                      height="12px"
                      as={MdContentCopy}
                    ></Icon>
                  ) : (
                    <Icon height="12px" as={TiTick}></Icon>
                  )}
                </span>
              </Tooltip>
            </Flex>
          </CopyToClipboard>
        </Flex>
      </Flex>

      <Flex alignItems="center" width="92%" paddingTop="3px">
        <Text
          textTransform="lowercase"
          color="grey2"
          align="center"
          width="100%"
          textAlign="start"
          textOverflow="hidden"
          fontSize="12px"
          paddingLeft="2px"
          overflow="hidden"
          fontWeight="500"
          whiteSpace="nowrap"
          textOverflow="ellipsis"
          // bg="red"
        >
          {!isNotHover && text}
        </Text>
      </Flex>
    </Flex>
  );
}

export default CopyToClipBoardComp;
