import { Flex } from '@chakra-ui/react';
import DisplayScreen from 'app/pages/DisplayScreen';
import NoContainDisplay from 'app/pages/DisplayScreen/components/NoContainDisplay';
import ScreenShotDisplay from 'app/pages/DisplayScreen/components/ScreenShotDisplay';
import { selectDownloadScreenshotsData } from 'app/pages/DownLoadLog/slice/selectors';
import React from 'react';
import { useSelector } from 'react-redux';

function ScreentshotsViewer({tabType}) {
  const displaySceenData = useSelector(selectDownloadScreenshotsData);
  return (
    <Flex
      marginTop="20px"
      justifyContent="flex-start"
      alignItems="center"
      flexDirection="column"
    >
      {displaySceenData.length > 0 ? (
        <DisplayScreen tabType= {tabType === 'mobile-interact' ?  'mobile-interact' :  "lr-interact" } orientation={tabType === 'mobile-interact' ?  'portrait' :  "landscape" }   deviceType={tabType === 'mobile-interact' ?  'mobile' :  "tv" }    ></DisplayScreen>
      ) : (
        <Flex flexDirection="column" margin="0px 20px" width="600px">
          <NoContainDisplay tabType={tabType === 'mobile-interact' ?  'mobile-interact' :  "lr-interact" }  ></NoContainDisplay>
        </Flex>
      )}
    </Flex>
  );
}

export default ScreentshotsViewer;
