import GetIdToken from 'Authentication/configuration/GetIdToken';
import { call, put, takeLatest, delay, takeEvery } from 'redux-saga/effects';
import { request } from 'utils/request';
import { DisplayScreenActions as actions } from '.';
import { DisplayScreenErrorType } from './types';
import { REACT_APP_AUTOMATION_TESTING_BASE_URL, REACT_APP_TEST_ANALYTICS_BASE_URL } from 'utils/enviroment';

const BASE_URL = REACT_APP_TEST_ANALYTICS_BASE_URL;

const BASE_URL_FILE = REACT_APP_AUTOMATION_TESTING_BASE_URL;


/**
 * Github repos request/response handler
 */
export function* getDisplayScreenData(params) {
  const idToken = GetIdToken();
  //console.log(params.payload);
  // yield delay(1000);
  //  const requestURL = `${BASE_URL}/test-session`;
  const requestURL = `${BASE_URL}/tests/${
    params.payload.testId
  }/device/resource/${params.payload.name}?testStatus=${localStorage.getItem(
    'testStatus',
  )}`;
  const config = {
    method: 'GET',
    headers: {
      'Content-type': 'application/json',
    },
  };

  try {
    // Call our request helper (see 'utils/request')
    const DisplayScreenData = yield call(request, requestURL, config);
    // console.log(DisplayScreenData);
    if (DisplayScreenData.hasOwnProperty('body')) {
      // console.log(DisplayScreenData.body.columns);

      yield put(
        actions.DisplayScreenDataLoaded(DisplayScreenData.body.columns),
      );
    } else {
      yield put(
        actions.DisplayScreenDataError(DisplayScreenErrorType.RESPONSE_ERROR),
      );
    }
  } catch (err) {
    /*
    if (err.response?.status === 404) {
      yield put(actions.repoError(RepoErrorType.USER_NOT_FOUND));
    } else if (err.message === 'Failed to fetch') {
      yield put(actions.repoError(RepoErrorType.GITHUB_RATE_LIMIT));
    } else {
    */
    yield put(
      actions.DisplayScreenDataError(DisplayScreenErrorType.RESPONSE_ERROR),
    );
  }
}

export function* getScreenshotsUrlData(fileDownloadInfo) {
  const idToken = GetIdToken();
  //console.log(fileDownloadInfo)
  const requestURL = `${BASE_URL}/download?testId=${fileDownloadInfo.payload.testId}&type=output&fileName=${fileDownloadInfo.payload.filename}`;
  const config = {
    method: 'GET',
    headers: {
      'Content-type': 'application/json',
    },
  };
  try {
    const DownloadFileUrl = yield call(request, requestURL, config);
    if (DownloadFileUrl.hasOwnProperty('data')) {
      yield put(actions.DisplayScreenshotsUrlLoaded(DownloadFileUrl.data.list));
    } else {
      yield put(
        actions.DisplayScreenshotsUrlError(
          DisplayScreenErrorType.RESPONSE_ERROR,
        ),
      );
    }
  } catch (e) {
    yield put(
      actions.DisplayScreenshotsUrlError(DisplayScreenErrorType.RESPONSE_ERROR),
    );
  }
}

function* getScreenshotUrlSagawather(fileDownloadInfo) {
  const idToken = GetIdToken();
  //console.log(fileDownloadInfo);
  const requestURL = `${BASE_URL_FILE}/download?testId=${fileDownloadInfo.payload.testId}&type=screenshots&fileName=${fileDownloadInfo.payload.filename}`;
  const config = {
    method: 'GET',
    headers: {
      'Content-type': 'application/json',
      Authorization: `Bearer ${idToken}`,
    },
  };
  try {
    const DownloadFileUrl = yield call(request, requestURL, config);
    if (DownloadFileUrl.hasOwnProperty('data')) {
      if (DownloadFileUrl.data.hasOwnProperty('list')) {
        yield put(actions.screenShotUrlLoaded(DownloadFileUrl.data.list.url));
      } else {
        yield put(
          actions.screenShotUrlError(DisplayScreenErrorType.RESPONSE_ERROR),
        );
      }
    } else {
      yield put(
        actions.screenShotUrlError(DisplayScreenErrorType.RESPONSE_ERROR),
      );
    }
  } catch (e) {
    yield put(
      actions.screenShotUrlError(DisplayScreenErrorType.RESPONSE_ERROR),
    );
  }
}

/**
 * Root saga manages watcher lifecycle
 */
export function* DisplayScreenSaga() {
  // Watches for loadRepos actions and calls getRepos when one comes in.
  // By using `takeLatest` only the result of the latest API call is applied.
  // It returns task descriptor (just like fork) so we can continue execution
  // It will be cancelled automatically on component unmount

  yield takeEvery(actions.loadDisplayScreenData.type, getDisplayScreenData);
  yield takeEvery(actions.loadDisplayScreenshots.type, getScreenshotsUrlData);
  yield takeLatest(actions.loadScreenShotUrl.type, getScreenshotUrlSagawather);
}
