import { AddIcon, MinusIcon } from '@chakra-ui/icons'
import { Accordion, AccordionButton, AccordionIcon, AccordionItem, AccordionPanel, Box, Flex, Text } from '@chakra-ui/react'
import React from 'react'

function AccordionComponent({children , title , isUrl , rightItem , isDefault }) {
  return (
    <>
            <Accordion padding='0px' allowToggle  defaultIndex={ isDefault ?  [0] : []} >


<AccordionItem borderBottom='0px solid white'  >

{({ isExpanded }) => (
      <>

    <AccordionButton background={'white'} padding= {isUrl && "2px" }_hover={{background: 'white'}} _focus='0px 0px 0px white' >
    <Flex  overflow='hidden' color="#747474"  width="100%" alignContent='center' alignItems="center" >
        <Text fontSize='13px'   whiteSpace="nowrap" textAlign="left" title={title} width="600px" overflow='hidden' textOverflow='ellipsis' >
        <Text as="span" padding="0px 10px" >
        {isExpanded ? (
              <MinusIcon pd="0px 5px" color="grey1" fontSize='12px' />
            ) : (
              <AddIcon pd="0px 5px" color="grey1" fontSize='12px' />
            )}
        </Text >
        { title}
       
        </Text>
        
        <>{rightItem}</>
    
        </Flex>
      {/* <AccordionIcon /> */}
  

    </AccordionButton>
  <AccordionPanel padding="0px">
 {isExpanded && children}
     </AccordionPanel>
     </>
    )}
</AccordionItem>
</Accordion>
    </>
  )
}

export default AccordionComponent