import { PayloadAction } from '@reduxjs/toolkit';
import { createSlice } from 'utils/@reduxjs/toolkit';
import { useInjectReducer, useInjectSaga } from 'utils/redux-injectors';
import { DisplayScreenSaga } from './saga';
import { DisplayScreenState } from './types';

export const initialState: DisplayScreenState = {
  loading: false,
  error: null,
  DisplayScreenData: [],
  loadingScreenshot: false,
  errorLoadingScreenshot: null,
  DisplayScreenshotUrl: {},
  loadingScreenShotUrl: false,
  screenShotUrl: '',
  screenShotUrlError: null,
};

const slice = createSlice({
  name: 'DisplayScreen',
  initialState,
  reducers: {
    loadDisplayScreenData: state => {
      state.loading = true;
    },
    DisplayScreenDataLoaded: (state, action: PayloadAction<any>) => {
      state.loading = false;
      state.error = null;
      // console.log(action.payload)
      if (action.payload.length === 0) {
        state.DisplayScreenData = [];
      } else {
        state.DisplayScreenData.push(action.payload);
      }
    },
    DisplayScreenDataError: (state, action: PayloadAction<any>) => {
      state.loading = false;
      state.error = action.payload;
      // state.DisplayScreenData = [];
    },
    loadDisplayScreenshots: state => {
      state.loadingScreenshot = true;
    },
    DisplayScreenshotsUrlLoaded: (state, action: PayloadAction<any>) => {
      state.loadingScreenshot = false;
      state.errorLoadingScreenshot = null;
      state.DisplayScreenshotUrl = action.payload;
    },
    DisplayScreenshotsUrlError: (state, action: PayloadAction<any>) => {
      state.loadingScreenshot = false;
      state.errorLoadingScreenshot = action.payload;
    },
    loadScreenShotUrl: state => {
      state.loadingScreenShotUrl = true;
    },
    screenShotUrlLoaded: (state, action: PayloadAction<any>) => {
      state.loadingScreenShotUrl = false;
      state.screenShotUrl = action.payload;
      state.screenShotUrlError = null;
    },
    screenShotUrlError: (state, action: PayloadAction<any>) => {
      state.loadingScreenShotUrl = false;
      state.screenShotUrlError = action.payload;
    },
  },
});

export const { actions: DisplayScreenActions } = slice;

export const useDisplayScreenSlice = () => {
  useInjectReducer({ key: slice.name, reducer: slice.reducer });
  useInjectSaga({ key: slice.name, saga: DisplayScreenSaga });
  return { actions: slice.actions };
};

