// @ts-nocheck
import { call, put, takeLatest, delay } from 'redux-saga/effects';
import { request } from 'utils/request';
import { DownloadLogActions as actions } from '.';
import { DownloadLogData } from 'types/DownloadLog';
import { DownloadLogErrorType } from './types';
import GetIdToken from 'Authentication/configuration/GetIdToken';
import { REACT_APP_AUTOMATION_TESTING_BASE_URL } from 'utils/enviroment';

const BASE_URL = REACT_APP_AUTOMATION_TESTING_BASE_URL;


/**
 * Github repos request/response handler
 */

//console.log(idToken);

export function* getDownloadLogData(id) {
  const idToken = GetIdToken();
  //console.log(id.payload)
  const requestURL = `${BASE_URL}/download?testId=${id.payload}&type=output`;
  const config = {
    method: 'GET',
    headers: {
      'Content-type': 'application/json',
      Authorization: `Bearer ${idToken}`,
    },
  };
  try {
    // Call our request helper (see 'utils/request')
    const DownloadLogData = yield call(request, requestURL, config);
    //console.log(DownloadLogData);
    if (DownloadLogData.hasOwnProperty('data')) {
      yield put(actions.DownloadLogDataLoaded(DownloadLogData.data.list));
    } else {
      yield put(
        actions.DownloadLogDataError(DownloadLogErrorType.RESPONSE_ERROR),
      );
    }
  } catch (err) {
    /*
    if (err.response?.status === 404) {
      yield put(actions.repoError(RepoErrorType.USER_NOT_FOUND));
    } else if (err.message === 'Failed to fetch') {
      yield put(actions.repoError(RepoErrorType.GITHUB_RATE_LIMIT));
    } else {
    */
    yield put(
      actions.DownloadLogDataError(DownloadLogErrorType.RESPONSE_ERROR),
    );
  }
}
