export function* getDownloadFileUrlData(fileDownloadInfo) {
  //console.log(fileDownloadInfo)
  const idToken = GetIdToken();
  const requestURL = `${BASE_URL}/download?testId=${fileDownloadInfo.payload.testId}&type=${fileDownloadInfo.payload.type}&fileName=${fileDownloadInfo.payload.filename}`;
  const config = {
    method: 'GET',
    headers: {
      'Content-type': 'application/json',
      Authorization: `Bearer ${idToken}`,
    },
  };
  try {
    const DownloadFileUrl = yield call(request, requestURL, config);
   
    if (DownloadFileUrl.hasOwnProperty('data')) {
      console.log({DownloadFileUrl})
      if(fileDownloadInfo.payload.hasOwnProperty('forUse') && fileDownloadInfo.payload.forUse === 'getData') {
        yield put(actions.getFileUrlLoaded(DownloadFileUrl.data.list));
      }else{
        yield put(actions.downloadFileUrlLoaded(DownloadFileUrl.data.list));
      }

    } else {
      yield put(
        actions.downloadFileUrlError(DownloadLogErrorType.RESPONSE_ERROR),
      );
    }
  } catch (e) {
    console.log(e)
    yield put(
      actions.downloadFileUrlError(DownloadLogErrorType.RESPONSE_ERROR),
    );
  }
}
export function* getDownloadVideoFileUrlData(fileDownloadInfo) {
  const idToken = GetIdToken();
  //console.log(fileDownloadInfo)
  const requestURL = `${BASE_URL}/download?testId=${fileDownloadInfo.payload.testId}&type=${fileDownloadInfo.payload.type}&fileName=${fileDownloadInfo.payload.filename}`;
  const config = {
    method: 'GET',
    headers: {
      'Content-type': 'application/json',
      Authorization: `Bearer ${idToken}`,
    },
  };
  try {
    const DownloadFileUrl = yield call(request, requestURL, config);
    if (DownloadFileUrl.hasOwnProperty('data')) {
      // console.log(DownloadFileUrl);
      yield put(
        actions.downloadVideoFileUrlLoaded(DownloadFileUrl.data.list.url),
      );
    } else {
      yield put(
        actions.downloadVideoFileUrlError(DownloadLogErrorType.RESPONSE_ERROR),
      );
    }
  } catch (e) {
    yield put(
      actions.downloadVideoFileUrlError(DownloadLogErrorType.RESPONSE_ERROR),
    );
  }
}

export function* DownloadLogSaga() {
  yield takeLatest(actions.loadDownloadFileUrl.type, getDownloadFileUrlData);
  yield takeLatest(
    actions.loadDownloadVideoFileUrl.type,
    getDownloadVideoFileUrlData,
  );
  yield takeLatest(actions.loadDownloadLogData.type, getDownloadLogData);
  yield takeLatest(
    actions.loadDownloadScreenshotsData.type,
    getDownloadScreenshotsData,
  );
}
