// @ts-nocheck
import { Accordion, AccordionItem, Flex, Select, Text } from '@chakra-ui/react';
import React, { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import SummaryCard from '../Components/SummaryCard';
import {
  selectHarChartData,
  selectHarIssueSummaryEntiesData,
} from '../slice/selector';

function IssueSummaryPopup() {
  const [filterHttpErrrorData, setFilterHttpErrorData] = useState([]);
  const [filterHttpError4xxData, setFilterHttpError4xxData] = useState([]);
  const [filterHttpError5xxData, setFilterHttpError5xxData] = useState([]);
  const [filterWaitTimeData, setFilterWaitTimeData] = useState([]);
  const [filterSlowuiThreadData, setFilterSlowuiThreadData] = useState([]);
  const [filterSSLData, setFilterSSLData] = useState([]);
  const [filterSlowNetworkData, setFilterSlowNetworkData] = useState([]);
  const [getFilterInputWaitTime, setFilterInputWaitTime] = useState(200);
  const [getFilterInputSlowUi, setFilterInputSlowUi] = useState(100);
  const [getFilterInputSSL, setFilterInputSSL] = useState(200);
  const [getFilterInputSLowNetWork, setFilterSlowNetWork] = useState(100);
  const EntiesData = useSelector(selectHarChartData);
  //console.log(EntiesData);

  useEffect(() => {
    // filterHttpData();
    filterHttp4xxData();
    filterHttp5xxData();
  }, [EntiesData]);

  useEffect(() => {
    filterWaitTime();
    filterSlowUiThread();
    filterSslTime();
    filterSlowNewworkTime();
  }, [
    getFilterInputWaitTime,
    getFilterInputSlowUi,
    getFilterInputSSL,
    getFilterInputSLowNetWork,
    EntiesData,
  ]);

  // const filterHttpData = () => {
  //   const data = EntiesData.filter(item => item.status > 399);
  //   setFilterHttpErrorData(data);
  // };
  const filterHttp4xxData = () => {
    const data = EntiesData.filter(
      item => item.status > 399 && item.status < 499,
    );
    setFilterHttpError4xxData(data);
  };
  const filterHttp5xxData = () => {
    const data = EntiesData.filter(item => item.status > 499);
    setFilterHttpError5xxData(data);
  };

  const filterWaitTime = () => {
    const data = EntiesData.filter(item => item.wait > getFilterInputWaitTime);
    setFilterWaitTimeData(data);
  };
  const filterSlowUiThread = () => {
    const data = EntiesData.filter(item => item.blocked > getFilterInputSlowUi);
    setFilterSlowuiThreadData(data);
  };
  const filterSslTime = () => {
    const data = EntiesData.filter(item => item.ssl > getFilterInputSSL);
    setFilterSSLData(data);
  };
  const filterSlowNewworkTime = () => {
    const data = EntiesData.filter(
      item => item.connect > getFilterInputSLowNetWork,
    );
    setFilterSlowNetworkData(data);
  };

  return (
    <Flex
      height="470px"
      marginBottom="5px"
      flexDirection="column"
      paddingBottom="20px"
    >
      <Flex
        flexDirection="column"
        overflowY="scroll"
        css={{
          '&::-webkit-scrollbar': {
            width: '8px',
            //height: '30px',
            border: '5px solid #F3F6F9',
          },
          '&::-webkit-scrollbar-track': {
            boxShadow: 'inset 0 0 6px white',
          },
          '&::-webkit-scrollbar-thumb': {
            backgroundColor: '#C1C1C1',
            borderRadius: '5px',
            boreder: '2px solid',
          },
        }}
      >
        <Accordion allowToggle>
          {/* <SummaryCard
            title="Https Error"
            data={filterHttpErrrorData}
          ></SummaryCard> */}

          <Flex flexDirection="column">
            <Text
              padding="2px 15px"
              fontWeight="600"
              color="grey13"
              backgroundColor="#EDEDED"
            >
              HTTP Errors
            </Text>
            <Flex flexDirection="column" paddingLeft="8px">
              <SummaryCard
                title="HTTP Error 4xx"
                data={filterHttpError4xxData}
              ></SummaryCard>
              <SummaryCard
                title="HTTP Error 5xx"
                data={filterHttpError5xxData}
              ></SummaryCard>
            </Flex>
          </Flex>

          <SummaryCard
            title="Wait Time"
            data={filterWaitTimeData}
            getFilterInput={getFilterInputWaitTime}
            setFilterInput={setFilterInputWaitTime}
          ></SummaryCard>

          <SummaryCard
            title="Blocked Time"
            getFilterInput={getFilterInputSlowUi}
            setFilterInput={setFilterInputSlowUi}
            data={filterSlowuiThreadData}
          ></SummaryCard>

          <SummaryCard
            title="SSL/TLS Time"
            getFilterInput={getFilterInputSSL}
            setFilterInput={setFilterInputSSL}
            data={filterSSLData}
          ></SummaryCard>
          <SummaryCard
            title="Connect Time"
            getFilterInput={getFilterInputSLowNetWork}
            setFilterInput={setFilterSlowNetWork}
            data={filterSlowNetworkData}
          ></SummaryCard>
        </Accordion>
      </Flex>
    </Flex>
  );
}

export default IssueSummaryPopup;
