import { Flex, Icon, Image } from '@chakra-ui/react';
import { selectDownloadScreenshotsData } from 'app/pages/DownLoadLog/slice/selectors';
import React, { useEffect, useState } from 'react';
import { AiOutlineArrowLeft, AiOutlineArrowRight } from 'react-icons/ai';
import { useDispatch, useSelector } from 'react-redux';
import { useParams } from 'react-router-dom';
import { useDisplayScreenSlice } from '../slice';
import { selectScreenshotUrl } from '../slice/selectors';

// const defaultImage = [
//   'https://s3.ap-south-1.amazonaws.com/hotstar.screenshot.png/271941/img_2022-03-21--10-18-28.jpg',
