function ScreenShotDisplay({ screenShotUrl, tabType , deviceType , orientation}) {
  const { actions } = useDisplayScreenSlice();
  const { testId } = useParams();
  const dispatch = useDispatch();
  const [slide, setSlide] = useState(0);
  const screenshotData = screenShotUrl !== undefined ? screenShotUrl : [];
  const [screen, setScreen] = useState(screenshotData[slide]);
  const selectedScreenshotUrl = useSelector(selectScreenshotUrl);

  useEffect(() => {
    dispatch(
      actions.loadScreenShotUrl({
        testId: testId,
        filename: screenshotData[slide],
      }),
    );
  }, []);

  const rightArrowHandler = () => {
    if (slide < screenshotData.length - 1) {
      const value = slide + 1;
      setSlide(value);
      setScreen(screenshotData[slide]);
      dispatch(
        actions.loadScreenShotUrl({
          testId: testId,
          filename: screenshotData[value],
        }),
      );
    }
  };
  const leftArrowHandler = () => {
    if (slide > 0) {
      const value = slide - 1;
      setSlide(value);
      setScreen(screenshotData[slide]);
      dispatch(
        actions.loadScreenShotUrl({
          testId: testId,
          filename: screenshotData[value],
        }),
      );
    }
  };

  const handleSlideChange = e => {
    //console.log(e.target.value)
    setSlide(parseInt(e.target.value));
    setScreen(screenshotData[slide]);
    dispatch(
      actions.loadScreenShotUrl({
        testId: testId,
        filename: screenshotData[parseInt(e.target.value)],
      }),
    );
  };

  // console.log(screen)
  // console.log(selectedScreenshotUrl)

  // useEffect(() => {
  //   dispatch(actions.loadDisplayScreenshots({ testId,  filename : screen } ))
  // }, [screen])

  return (
    <Flex
      justifyContent="flex-start"
      alignItems="center"
      flexDirection="column"
    >
      <Flex
        // bg="black"
        // borderRadius="10px"
        // bg="white"
        // border="4px solid #3F4254"
        // width="180px"
        // height="379px"
        alignContent="center"
        justifyContent="center"
      >
        <Image
          borderRadius="8px"
          bg="black"
          padding=".5px"
          border="5px solid #3F4254"
          objectFit="fill"
          // borderRadius="7px"
          //objectFit="cover"
          height="379px"
          src={selectedScreenshotUrl}
          alt="NO content avaliable"
          width={
            (tabType === 'lr-interact' || deviceType === 'tv'|| orientation ==='landscape')
              ? '600px'
              : '180px'
          }
        ></Image>
      </Flex>

      <Flex
        justifyContent="space-evenly"
        width="185px"
        padding="10px 25px"
        alignContent="center"
      >
        <Flex
          alignContent="center"
          onClick={leftArrowHandler}
          justifyContent="center"
          disabled={slide <= 0 ? true : false}
          width="25px"
          height="25px"
          color="red1"
        >
          <Icon
            data-icon="left-screenshot-arrow"
            width="24px"
            height="24px"
            as={AiOutlineArrowLeft}
            cursor="pointer"
          ></Icon>
        </Flex>
        <Flex
          alignContent="center"
          onClick={rightArrowHandler}
          disabled={slide >= screenshotData.length - 1 ? true : false}
          justifyContent="center"
          color="red1"
          width="25px"
          height="25px"
        >
          <Icon
            // onClick={stopScreenShotHandler}
            data-icon="right-screenshot-arrow"
            width="24"
            marginLeft="4"
            height="6"
            cursor="pointer"
            as={AiOutlineArrowRight}
          ></Icon>
        </Flex>
      </Flex>
      <Flex
        justifyContent="space-evenly"
        width="185px"
        padding="0px 25px"
        alignContent="center"
      >
        <input
          data-input-slider="slider-screenshots"
          style={{ width: '170px', marginTop: '5px' }}
          type="range"
          min={0}
          max={screenshotData.length - 1}
          step={1}
          value={slide}
          // onMouseDown={handleSeekMouseDown}
          onChange={handleSlideChange}
        />
      </Flex>
    </Flex>
  );
}

export default ScreenShotDisplay;
