// @ts-nocheck
import { Flex, Icon } from '@chakra-ui/react';
import React, { useRef, useState } from 'react';
import ReactPlayer from 'react-player';
import mobile from '../../../../styles/Assets/display1.png';
import play from '../../../../styles/Assets/Play.svg';
import stop from '../../../../styles/Assets/Stop.svg';
import { BsPauseCircle, BsPlayCircle, BsStopCircle } from 'react-icons/bs';
//import samplesrc  from '../Assets/js_demo.mp4'
function VideoDisplay({ videoUrl, tabType, deviceType , orientation}) {
  const [playing, setPlaying] = useState(false);
  const [played, setPlayed] = useState(0);
  const [seeking, setSeeking] = useState(false);
  const [url, setUrl] = useState(videoUrl);
  const playerRef = useRef();
  //console.log(videoUrl);
  const playPauseHandler = () => {
    setPlaying(!playing);
  };
  const stopHandler = () => {
    setPlaying(false);
    setPlayed(0);
    playerRef.current && playerRef.current.seekTo(0);
  };
  const handleSeekMouseDown = () => {
    //console.log('handleSeekMouse')
    setSeeking(true);
  };

  const handleSeekChange = e => {
    setPlayed(parseFloat(e.target.value));
    // console.log(played)
  };
  const handleSeekMouseUp = e => {
    //console.log('handleSeekMouseUp')
    //console.log(parseFloat(e.target.value))
    setSeeking(false);
    playerRef.current && playerRef.current.seekTo(parseFloat(e.target.value));
  };

  return (
    <Flex
      justifyContent="flex-start"
      alignItems="center"
      flexDirection="column"
    >
      <Flex
        width={(tabType === 'lr-automation' || deviceType ==='tv' || orientation === 'landscape') ? '600px' : '180px'}
        //height="379px"
        borderRadius="8px"
        bg="#3F4254"
        border="4px solid #3F4254"
        alignItems="center"
        justifyContent="center"
      >
        <ReactPlayer
          ref={playerRef}
          style={{
            borderRadius: '10px',
            objectFit: 'cover',
            backgroundColor: 'black',
          }}
          //type="video/avi"
          playing={playing}
          url={videoUrl}
          //   url="https://mozark-builds.s3.ap-south-1.amazonaws.com/65432.avi"
          //url  ={{src: samplesrc , type: 'video/mp4'} }
          //url='https://mozark-builds.s3.ap-south-1.amazonaws.com/webcam_video-test.avi'
          width={(tabType === 'lr-automation'|| deviceType === 'tv' || orientation === 'landscape') ? '600px' : '180px'}
          //height="370px"
          height={(tabType === 'lr-automation' || deviceType === 'tv' || orientation === 'landscape') ? '370px' : '370px'}
          onProgress={data => setPlayed(data.played)}
        ></ReactPlayer>
      </Flex>
      <input
        style={{ width: '170px', marginTop: '8px' }}
        type="range"
        min={0}
        max={0.999999}
        step="any"
        value={played}
        onMouseDown={handleSeekMouseDown}
        onChange={handleSeekChange}
        onMouseUp={handleSeekMouseUp}
      />

      {/* <progress max={1} value={played} /> */}

      <Flex
        justifyContent="space-evenly"
        width="185px"
        padding="10px 25px"
        alignContent="center"
      >
        <Flex
          alignContent="center"
          onClick={playPauseHandler}
          justifyContent="center"
          color="#094284"
          width="25px"
          height="25px"
        >
          {playing ? (
            <Icon
              width="24px"
              height="24px"
              as={BsPauseCircle}
              cursor="pointer"
            ></Icon>
          ) : (
            <Icon
              width="24px"
              height="24px"
              as={BsPlayCircle}
              cursor="pointer"
            ></Icon>
          )}
        </Flex>
        <Flex
          alignContent="center"
          // onClick={playPauseHandler}
          justifyContent="center"
          color="red1"
          width="25px"
          height="25px"
        >
          <Icon
            onClick={stopHandler}
            width="24"
            marginLeft="4"
            height="6"
            cursor="pointer"
            as={BsStopCircle}
          ></Icon>
        </Flex>
      </Flex>
    </Flex>
  );
}

export default VideoDisplay;
