//@ts-nocheck
import React, { useEffect } from 'react';

import { useSelector, useDispatch } from 'react-redux';
import {
  selectsummaryTableData,
  selectLoading,
  selectError,
} from './slice/selectors';
import { summaryTableErrorType } from './slice/types';
import { useSummaryTableSlice } from './slice';
import SummaryTableComp from 'app/components/SummaryTableComp';
import { Flex, Text } from '@chakra-ui/react';
import NoContent from 'app/components/NoContent';
import TestCasesTable from '../TestCases/components/TestCasesTable';
import { useParams } from 'react-router-dom';
import LoadingComponent from 'app/components/LoadingComponent';
import { selectTestSessionData } from '../TestSession/slice/selectors';
//const tableHeader = ['Status', 'Time', 'Test Case', 'Details'];
const EventSummaryTableHeading = [
  'Event Name',
  'Time',
  'Test Case Name',
  // 'KPI Name',
];
export function SummaryTable() {
  const { actions } = useSummaryTableSlice();
  const { testId } = useParams();
  const testSessionData = useSelector(selectTestSessionData);
  const summaryTableData = useSelector(selectsummaryTableData);
  const isLoading = useSelector(selectLoading);
  const error = useSelector(selectError);
  const { testStatus } = testSessionData;
  const dispatch = useDispatch();
  console.log({ testStatus });
  useEffect(() => {
    let intervalId;
    const loadData = () => {
      dispatch(actions.loadsummaryTableData(testId));
    };

    if (testStatus === 'Started' || testStatus === 'Scheduled') {
      intervalId = setInterval(loadData, 5000);
    } else {
      loadData();
    }

    return () => {
      clearInterval(intervalId);
      dispatch(actions.summaryTableDataLoaded([]));
    };
  }, [testId, testStatus]);

  // const onSubmitForm = (evt?: React.FormEvent<HTMLFormElement>) => {
  //   /* istanbul ignore next  */
  //   if (evt !== undefined && evt.preventDefault) {
  //     evt.preventDefault();
  //   }
  // };

  //sort events data based on timestamp assending order
  const sortedTimestamps = summaryTableData.slice();
  if (sortedTimestamps.length > 0) {
    sortedTimestamps.sort(function (a, b) {
      return new Date(a.time) - new Date(b.time);
    });
  }

  return (
    <Flex width="100%">
      {isLoading ? (
        <LoadingComponent></LoadingComponent>
      ) : (
        <TestCasesTable
          error={error}
          tableHeader={EventSummaryTableHeading}
          TestCaseSummaryTableData={sortedTimestamps}
          title="Event Summary"
        ></TestCasesTable>
      )}
    </Flex>
  );
}

export const repoErrorText = (error: summaryTableErrorType) => {
  switch (error) {
    case summaryTableErrorType.RESPONSE_ERROR:
      return 'There is no such user ';
    default:
      return 'An error has occurred!';
  }
};
