import { call, put, takeLatest, delay } from 'redux-saga/effects';
import { request } from 'utils/request';
import { testSessionActions as actions } from '.';
import { TestSessionData } from 'types/TestSession';
import { TestSessionErrorType } from './types';
import GetIdToken from 'Authentication/configuration/GetIdToken';
import { REACT_APP_AUTOMATION_TESTING_BASE_URL, REACT_APP_TEST_ANALYTICS_BASE_URL } from 'utils/enviroment';

/**
 * Github repos request/response handler
 */


const BASE_URL = REACT_APP_TEST_ANALYTICS_BASE_URL;
const BASE_URL_TEST_EXECUTE = REACT_APP_AUTOMATION_TESTING_BASE_URL;
////console.log(BASE_URL)

export function* getTestSessionData(id) {
  const idToken = GetIdToken();
  const testId = id.payload;

  const requestURL = `${BASE_URL}/tests/${testId}/info`;
  // const requestURL = `${BASE_URL}/tests/${testId}/info?testStatus=${localStorage.getItem(
  //   'testStatus',
  // )}`;

  const config = {
    method: 'GET',
    headers: {
      'Content-type': 'application/json',
      Authorization: `Bearer ${idToken}`,
    },
  };

  try {
    // Call our request helper (see 'utils/request')
    const testSessionData = yield call(request, requestURL, config);
    //console.log(testSessionData.body)
    if (testSessionData.hasOwnProperty('body')) {
      // console.log(testSessionData)
      yield put(actions.testSessionDataLoaded(testSessionData.body));
    } else {
      yield put(
        actions.testSessionDataError(TestSessionErrorType.RESPONSE_ERROR),
      );
      // console.log("can not get response")
    }
  } catch (err) {
    yield put(
      actions.testSessionDataError(TestSessionErrorType.RESPONSE_ERROR),
    );
    //  //console.log(err)
  }
}

function* abortTestSagaWacher(testId) {
  const idToken = GetIdToken();
  const requestUrl = `${BASE_URL_TEST_EXECUTE}/tests`; //'https://development-api.mozark.ai/testexecute/tests'

  const config = {
    method: 'PUT',
    headers: {
      'Content-Type': 'application/json',
      Authorization: `Bearer ${idToken}`,
    },
    body: JSON.stringify({ testId: testId.payload }),
  };

  try {
    const abortTestApiResponse = yield call(request, requestUrl, config);
    //console.log(abortTestApiResponse);
    if (abortTestApiResponse.hasOwnProperty('status')) {
      yield put(actions.abortedTestResponse(abortTestApiResponse));
    } else {
      yield put(actions.abortTestError(TestSessionErrorType.RESPONSE_ERROR));
    }
  } catch (err) {
    yield put(actions.abortTestError(TestSessionErrorType.RESPONSE_ERROR));
  }
}

/**
 * Root saga manages watcher lifecycle
 */
export function* testSessionSaga() {
  // Watches for loadRepos actions and calls getRepos when one comes in.
  // By using `takeLatest` only the result of the latest API call is applied.
  // It returns task descriptor (just like fork) so we can continue execution
  // It will be cancelled automatically on component unmount
  yield takeLatest(actions.loadAbortTest.type, abortTestSagaWacher);
  yield takeLatest(actions.loadTestSessionData.type, getTestSessionData);
}
