// @ts-nocheck
import GetIdToken from 'Authentication/configuration/GetIdToken';
import { call, delay, put, takeLatest } from 'redux-saga/effects';

import { request } from 'utils/request';
import { HarChartActions as actions } from '.';
import { REACT_APP_TEST_ANALYTICS_BASE_URL } from 'utils/enviroment';
const BASE_URL = REACT_APP_TEST_ANALYTICS_BASE_URL;

function* getHarKpiData(id) {
  const idToken = GetIdToken();
  const requestURL = `${BASE_URL}/tests/${id.payload}/app/resource/httpapi`;
  const config = {
    method: 'GET',
    headers: {
      'Content-type': 'application/json',
      Authorization: `Bearer ${idToken}`,
    },
  };
  try {
    const harKpiData = yield call(request, requestURL, config);
    //console.log(harKpiData);
    if (Object.keys(harKpiData).length !== 0) {
      if (harKpiData.hasOwnProperty('body')) {
        yield put(actions.HarHttpKpiDataLoaded(harKpiData.body));
      } else {
        yield put(actions.HarHttpKpiDataError(harKpiData.errorMessage));
      }
    } else {
      yield put(actions.HarHttpKpiDataError('could not get'));
    }
  } catch (err) {
    yield put(actions.HarHttpKpiDataError('could not get'));
  }
}
function* getHarChartData(payload) {
  const requestURL = `${payload.payload}`;

  try {
    // const harChartData = yield call(request, requestURL);
    const response = yield fetch(requestURL); //yield call(request, requestURL);
    const harChartData = yield response.json();

    if (
      harChartData.hasOwnProperty('statusCode') &&
      harChartData.statusCode === 200
    ) {
      yield put(actions.HarChartDataResponse(harChartData));
    } else {
      yield put(actions.HarHttpKpiDataError('Unable to read Har'));
    }
  } catch (err) {
    console.log({ err });
    yield put(actions.HarHttpKpiDataError('Unable to read Har'));
  }
}

export function* HarChartSaga() {
  yield takeLatest(actions.loadHarHttpKpi.type, getHarKpiData);
  yield takeLatest(actions.loadHarChart.type, getHarChartData);
}
