// @ts-nocheck
import { AspectRatio, Flex } from '@chakra-ui/react';
import LoadingComponent from 'app/components/LoadingComponent';
import NoContent from 'app/components/NoContent';
import React from 'react';
import { useSelector } from 'react-redux';
import {
  selectLoading,
  selectTestSessionData,
} from '../TestSession/slice/selectors';

export function LiveLog() {
  const testSessionData = useSelector(selectTestSessionData);
  //console.log(testSessionData);
  const isLoading = useSelector(selectLoading);

  let getlivelogUrl = testSessionData.hasOwnProperty('extra')
    ? testSessionData.extra.liveLog
    : undefined;
  const getliveLogFlag =
    testSessionData.hasOwnProperty('testConfiguration') &&
    testSessionData.testConfiguration.hasOwnProperty('captureLiveLogs') &&
    testSessionData.testConfiguration.captureLiveLogs;
  //console.log('livelog url', getliveLogFlag);
  return (
    <Flex
      marginTop="10px"
      fontFamily="Montserrat"
      fontWeight="500"
      fontSize="14px"
      flexDirection="column"
      background="white1"
      width="100%"
      padding="20px 10px"
      color="grey15"
      boxShadow="2px 8px 6px #00000029"
    >
      {isLoading ? (
        <LoadingComponent></LoadingComponent>
      ) : (
        <>
          {getliveLogFlag ? (
            <Flex
              flexDirection="column"
              //height="550px"
              width="100%"
              borderRadius="5px"
            >
              <Flex
                overflowY="scroll"
                css={{
                  '&::-webkit-scrollbar': {
                    width: '8px',
                    //height: '30px',
                    border: '5px solid #F3F6F9',
                  },
                  '&::-webkit-scrollbar-track': {
                    boxShadow: 'inset 0 0 6px white',
                  },
                  '&::-webkit-scrollbar-thumb': {
                    backgroundColor: '#C1C1C1',
                    borderRadius: '5px',
                    boreder: '2px solid',
                  },
                }}
                flexDirection="column"
              >
                <iframe
                  title="livelog"
                  src={getlivelogUrl}
                  allowFullScreen
                  width="100%"
                  height="540px"
                ></iframe>
              </Flex>
            </Flex>
          ) : (
            <NoContent message="No LIVE LOG Selected"> </NoContent>
          )}
        </>
      )}
    </Flex>
  );
}

export default LiveLog;
