import React from 'react';
import { Pie } from 'react-chartjs-2';
import { Flex } from '@chakra-ui/layout';
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js';

ChartJS.register(ArcElement, Tooltip, Legend);
function PieChart({ pieData, label, sliceBackgroundColor }) {
  // console.log({pieData, label , sliceBackgroundColor});

  const pieOptions = {
    maintainAspectRatio: false,
    responsive: true,

    plugins: {
      legend: {
        display: true,

        position: 'bottom',
        align: 'start',

        labels: {
          boxWidth: 10,
          padding: 8,
        },

        maxWidth: 200,
        // maxHeight : 200 ,
        maxHeight: 150,

        padding: 0,
      },
    },

    elements: {
      arc: {
        borderWidth: 0,
      },
    },
  };

  const data = {
    labels: pieData.labels,
    datasets: [
      {
        data: pieData.values,
        backgroundColor: sliceBackgroundColor,
        hoverBackgroundColor: '#FBC2C0',
        hoverBorderColor: '#F1150E',
      },
    ],
  };

  return (
    <Flex width="210px">
      <Pie data={data} options={pieOptions}></Pie>
    </Flex>
  );
}

export default PieChart;
