// @ts-nocheck
import { Flex, Text } from '@chakra-ui/react';
import NoContent from 'app/components/NoContent';
import SummaryTableComp from 'app/components/SummaryTableComp';
import React from 'react';

function TestCasesTable({
  title,
  isLoading,
  tableHeader,
  TestCaseSummaryTableData,
  error,
}) {
  return (
    <Flex
      marginTop="10px "
      fontFamily="Montserrat"
      fontWeight="500"
      fontSize="14px"
      flexDirection="column"
      background="white1"
      padding="10px"
      width="100%"
      boxShadow="2px 8px 6px #00000029"
    >
      <Text paddingBottom="1" paddingLeft="0.5">
        {title}
      </Text>
      {error === null ? (
        <>
          {TestCaseSummaryTableData.length > 0 ? (
            <SummaryTableComp
              tableHeader={tableHeader}
              isLoading={isLoading}
              summaryTableData={TestCaseSummaryTableData}
              title={title}
            ></SummaryTableComp>
          ) : (
            <NoContent
              height="150px"
              message="No Content Available"
            ></NoContent>
          )}
        </>
      ) : (
        <NoContent height="150px" message="Something Went Wrong"></NoContent>
      )}
    </Flex>
  );
}

export default TestCasesTable;
