// @ts-nocheck
import { Flex, Img, useDisclosure } from '@chakra-ui/react';
import PopupComp from 'app/components/PopupComp';
import React, { useState } from 'react';
import IssueSummaryPopup from './IssueSummaryPopup';
import SummaryPopCard from './SummaryPopCard';
import Summary from '../../../../styles/Assets/Summary.svg';
import SummaryActive from '../../../../styles/Assets/SummaryActive.svg';
import Issuesummary from '../../../../styles/Assets/Issue Summary.svg';
import IssuesummaryActive from '../../../../styles/Assets/Issue SummaryActive.svg';
function HarSummaryPopup() {
  // const [activePopup, setActivePopup] = useState(false);
  const { isOpen, onOpen, onClose } = useDisclosure();
  const [togglePopup, setTogglePopup] = useState(false);

  function popupHandler() {
    onOpen();
  }

  function issueSummaryHandler() {
    setTogglePopup(false);
  }
  function summaryHandler() {
    setTogglePopup(true);
  }

  const issueAction = () => {
    popupHandler();
    issueSummaryHandler();
  };

  const summaryAction = () => {
    popupHandler();
    summaryHandler();
  };
  //console.log('pop root render');
  //console.log(isOpen);

  return (
    <Flex>
      <Flex
        position="absolute"
        flexDirection="column"
        width="35px"
        boxShadow="0px 3px 6px #00000029"
        borderRadius="2px"
        height="80px"
        zIndex="1000"
        background="white"
        right="-30px"
        top="260px"
        alignItems="center"
        justifyContent="space-around"
      >
        <Flex
          justifyContent="center"
          alignItems="center"
          height="30px"
          width="30px"
          onClick={issueAction}
        >
          {!isOpen && (
            <Img
              cursor="pointer"
              src={Issuesummary}
              backgroundColor="transparent"
              alt="issue"
            ></Img>
          )}
          {(isOpen && !togglePopup && (
            <Img
              cursor="pointer"
              src={IssuesummaryActive}
              backgroundColor="transparent"
              alt="issue"
            ></Img>
          )) ||
            (isOpen && togglePopup && (
              <Img
                cursor="pointer"
                src={Issuesummary}
                backgroundColor="transparent"
                alt="issue"
              ></Img>
            ))}
        </Flex>
        <Flex
          justifyContent="center"
          alignItems="center"
          height="30px"
          width="30px"
          onClick={summaryAction}
        >
          {!isOpen && (
            <Img
              cursor="pointer"
              src={Summary}
              backgroundColor="transparent"
              alt="summary"
            ></Img>
          )}
          {(isOpen && togglePopup && (
            <Img
              cursor="pointer"
              src={SummaryActive}
              backgroundColor="transparent"
              alt="summary"
            ></Img>
          )) ||
            (isOpen && !togglePopup && (
              <Img
                cursor="pointer"
                src={Summary}
                backgroundColor="transparent"
                alt="summary"
              ></Img>
            ))}
        </Flex>
      </Flex>

      {/* summay popup */}
      {togglePopup && (
        <PopupComp onClose={onClose} title="Summary" activePopup={isOpen}>
          <SummaryPopCard></SummaryPopCard>
        </PopupComp>
      )}

      {/* isuue summary */}
      {!togglePopup && (
        <PopupComp activePopup={isOpen} onClose={onClose} title="Issue Summary">
          <IssueSummaryPopup></IssueSummaryPopup>
        </PopupComp>
      )}
    </Flex>
  );
}

export default HarSummaryPopup;
