import { createSelector } from '@reduxjs/toolkit';

import { RootState } from 'types';
import { initialState } from '.';

// First select the relevant part from the state
const selectSlice = (state: RootState) => state.DownloadLog || initialState;

export const selectDownloadLogData = createSelector(
  [selectSlice],
  state => state.DownloadLogData,
);
export const selectDownloadScreenshotsData = createSelector(
  [selectSlice],
  state => state.DownloadScreenshotsData,
);

export const selectLoading = createSelector(
  [selectSlice],
  state => state.loading,
);
export const selectLoadingScreenshots = createSelector(
  [selectSlice],
  state => state.loadingScreenshots,
);

export const selectError = createSelector([selectSlice], state => state.error);

export const selectDownloadFileUrl = createSelector(
  [selectSlice],
  state => state.DownloadFileUrl,
);
export const selectgetFileUrl = createSelector(
  [selectSlice],
  state => state.getFileUrlData,
);

export const selectDownloadfileUrlError = createSelector(
  [selectSlice],
  state => state.errorFileUrl,
);

export const selectDownloadScreenshotError = createSelector(
  [selectSlice],
  state => state.errorScreenshots,
);
export const selectDownloadFileError = createSelector(
  [selectSlice],
  state => state.error,
);
export const selectVideoFileName = createSelector(
  [selectSlice],
  state => state.videoFileName,
);
export const selectVideoFileUrl = createSelector(
  [selectSlice],
  state => state.DownloadVideoFileUrl,
);

