// @ts-nocheck
import { PayloadAction } from '@reduxjs/toolkit';
import { createSlice } from 'utils/@reduxjs/toolkit';
import { useInjectReducer, useInjectSaga } from 'utils/redux-injectors';
import { HarChartSaga } from './saga';
import { HarChartState } from './types';

const getEntityData = (entiesData, data) => {
  //console.log(entiesData, data);
  let enties = [];
  let issueSummaryEnties = [];
  const pageTime = data.hasOwnProperty('pages')
    ? new Date(data.pages[0].startedDateTime).valueOf()
    : 0;
  const startPageTime =
    pageTime === 0
      ? new Date(entiesData[0].startedDateTime).valueOf()
      : pageTime;//undefine
    //  console.log({pageTime  , startPageTime});
  for (let i = 0; i < entiesData.length; i++) {
    // console.log(entiesData[i])
    const startEntryTime = new Date(entiesData[i].startedDateTime).valueOf();
    
    const begningTime =
      startEntryTime - startPageTime <= 0 ? 0 : startEntryTime - startPageTime;
    // const begningTime =    startEntryTime - startPageTime;
    const connectTime =
      entiesData[i].timings.hasOwnProperty('connect') &&
      entiesData[i].timings.connect > 0
        ? entiesData[i].timings.connect
        : 0;

    const receiveTime =
      entiesData[i].timings.hasOwnProperty('receive') &&
      entiesData[i].timings.receive > 0
        ? entiesData[i].timings.receive
        : 0;
    const sendTime =
      entiesData[i].timings.hasOwnProperty('send') &&
      entiesData[i].timings.send > 0
        ? entiesData[i].timings.send
        : 0;
    const sslTime =
      entiesData[i].timings.hasOwnProperty('ssl') &&
      entiesData[i].timings.ssl > 0
        ? entiesData[i].timings.ssl
        : 0;
    const waitTime =
      entiesData[i].timings.hasOwnProperty('wait') &&
      entiesData[i].timings.wait > 0
        ? entiesData[i].timings.wait
        : 0;
    const blockedTime =
      entiesData[i].timings.hasOwnProperty('blocked') &&
      entiesData[i].timings.blocked > 0
        ? entiesData[i].timings.blocked
        : 0;
    const blockedOpen = begningTime;
    const blockedClose = begningTime + blockedTime;
    const sslOpen = blockedClose;
    const sslClose = sslOpen + sslTime;
    const connectOpen = sslClose;
    const connectClose = connectOpen + connectTime;
    const sendOpen = connectClose;
    const sendClose = sendOpen + sendTime;
    const waitOpen = sendClose;
    const waitClose = waitOpen + waitTime;
    const receiveOpen = waitClose;
    const receiveClose = receiveOpen + receiveTime;
    const reciveTooltip =
      entiesData[i].timings.receive >= 0
        ? entiesData[i].timings.receive.toFixed(1) + 'ms'
        : 'does not apply';
    const connectTooltip =
      entiesData[i].timings.connect >= 0
        ? entiesData[i].timings.connect.toFixed(1) + 'ms'
        : 'does not apply';
    const sendTooltip =
      entiesData[i].timings.send >= 0
        ? entiesData[i].timings.send.toFixed(1) + 'ms'
        : 'does not apply';
    const sslTooltip =
      entiesData[i].timings.ssl >= 0
        ? entiesData[i].timings.ssl.toFixed(1) + 'ms'
        : 'does not apply';
    const waitTooltip =
      entiesData[i].timings.wait >= 0
        ? entiesData[i].timings.wait.toFixed(1) + 'ms'
        : 'does not apply';
    const blockedTooltip =
      entiesData[i].timings.blocked >= 0
        ? entiesData[i].timings.blocked.toFixed(1) + 'ms'
        : 'does not apply';
    enties.push({
      category: '[' + i + '] ' + entiesData[i].request.url,
      status: entiesData[i].response.status,
      AssetType: entiesData[i].response.content.mimeType,
      beginingTime: begningTime,
      totalTime: entiesData[i].time,
      connect: entiesData[i].timings.connect,
      receive: entiesData[i].timings.receive,
      send: entiesData[i].timings.send,
      ssl: entiesData[i].timings.ssl,
      wait: entiesData[i].timings.wait,
      blocked: entiesData[i].timings.blocked,
      blockedOpen: blockedOpen,
      blockedClose: blockedClose,
      sslOpen: sslOpen,
      sslClose: sslClose,
      connectOpen: connectOpen,
      connectClose: connectClose,
      sendOpen: sendOpen,
      sendClose: sendClose,
      waitOpen: waitOpen,
      waitClose: waitClose,
      receiveOpen: receiveOpen,
      receiveClose: receiveClose,
      reciveTooltip: reciveTooltip,
      waitTooltip: waitTooltip,
      connectTooltip: connectTooltip,
      sendTooltip: sendTooltip,
      sslTooltip: sslTooltip,
      blockedTooltip: blockedTooltip,
    });
    issueSummaryEnties.push({
      category: entiesData[i].request.url,
      status: entiesData[i].response.status,

      AssetType: entiesData[i].response.content.mimeType,
      beginingTime: begningTime,
      time: entiesData[i].time,
      connect: entiesData[i].timings.connect,
      receive: entiesData[i].timings.receive,
      send: entiesData[i].timings.send,
      ssl: entiesData[i].timings.ssl,
      wait: entiesData[i].timings.wait,
      blocked: entiesData[i].timings.blocked,
    });
  }

  return { enties, issueSummaryEnties };
};
const getHarData = data => {
  //console.log(data.pages[0]);
  const startTime =
    data.hasOwnProperty('pages') && data.pages[0].startedDateTime;
  const onContentLoad =
    data.hasOwnProperty('pages') && data.pages[0].pageTimings.onContentLoad;
  const onLoadTime =
    data.hasOwnProperty('pages') && data.pages[0].pageTimings.onLoad;
  return {
    startTime,
    onContentLoad,
    onLoadTime,
  };
};
const getAllEntiesData = data => {
  let allEnties = [];
for(let i = 0; i < data.length; i++) {
   for(let j = 0; j < data[i].entries.length; j++) {
   allEnties.push(...data[i].entries[j].entries)
   }
}
//console.log({allEnties})
return allEnties;
}
export const initialState: HarChartState = {
  Hardata: {},
  errors: null,
  loading: false,
  HarEntiesData: [],
  HarGroupByData: [],
  HarIssueSummaryEnties: [],
  loadingUrl: false,
  harKpiData: {},
  harUrlData: null,
  harKpiErrors: null,
};

const slice = createSlice({
  name: 'harChart',
  initialState,
  reducers: {
    loadHarChart: state => {
      state.loading = true;
    },
    HarChartDataLoaded: (state, action: PayloadAction<any>) => {
      //  console.log(action.payload);
      if (
        Object.keys(action.payload).length !== 0 &&
        Object.keys(action.payload.entries).length < 1000
      ) {
        const { enties, issueSummaryEnties } = getEntityData(
          action.payload.entries,
          action.payload,
        );
        state.HarEntiesData = enties;
        state.HarIssueSummaryEnties = issueSummaryEnties;
        state.HarData = getHarData(action.payload);
        state.loading = false;
      } else if (Object.keys(action.payload.entries).length > 1000) {
        const { enties, issueSummaryEnties } = getEntityData(
          action.payload.entries.slice(0, 800),
          action.payload,
        );
        state.HarEntiesData = enties;
        state.HarIssueSummaryEnties = issueSummaryEnties;
        state.HarData = getHarData(action.payload);
        state.loading = false;
      } else {
        state.HarEntiesData = [];
        state.HarIssueSummaryEnties = [];
        state.HarData = {};
        state.loading = false;
      }
      //   console.log(action.payload.pages)
    },
    HarChartDataResponse : (state , action : PayloadAction<any>) => {
      //console.log({data : action.payload})
      state.HarEntiesData = action.payload.allEntries;//getAllEntiesData(action.payload);
      state.HarGroupByData = action.payload.body;
      state.loading = false;
      state.errors = null;
      state.HarIssueSummaryEnties = [];
    },
    HarChartDataError: (state, action: PayloadAction<any>) => {
      state.errors = action.payload;
      state.loading = false;
    },
    loadHarHttpKpi: (state, action: PayloadAction<any>) => {
      state.loadingUrl = true;
    },
    resetChartData : (state , action : PayloadAction<any>) => {
      console.log('reset hit ')
      state.HarEntiesData = [];
    },
    HarHttpKpiDataLoaded: (state, action: PayloadAction<any>) => {
      state.loadingUrl = false;
      state.harKpiData = action.payload;
      //state.harUrlData = action.payload.datas[0].url;
      state.harKpiErrors = null;
    },
    HarHttpKpiDataError: (state, action: PayloadAction<any>) => {
      state.harKpiData = {};
      state.harUrlData = null;
      state.loadingUrl = false;
      state.harKpiErrors = action.payload;
    },
  },
});

export const { actions: HarChartActions } = slice;

export const useHarChartSlice = () => {
  useInjectReducer({ key: slice.name, reducer: slice.reducer });
  useInjectSaga({ key: slice.name, saga: HarChartSaga });
  return { harActions: slice.actions };
};
