export default TestCases;

export const repoErrorText = (error: TestCasesErrorType) => {
  switch (error) {
    case TestCasesErrorType.RESPONSE_ERROR:
      return 'There is no such user ';
    default:
      return 'An error has occurred!';
  }
};
