// @ts-nocheck
import { PayloadAction } from '@reduxjs/toolkit';
import { createSlice } from 'utils/@reduxjs/toolkit';
import { useInjectReducer, useInjectSaga } from 'utils/redux-injectors';
import { DownloadLogSaga } from './saga';
import { DownloadLogState } from './types';

export const initialState: DownloadLogState = {
  loading: false,
  error: null,
  DownloadLogData: [],
  loadingScreenshots: false,
  errorScreenshots: null,
  DownloadScreenshotsData: [],
  loadingFileUrl: false,
  errorFileUrl: null,
  DownloadFileUrl: {},
  getFileUrlData: {},
  loadingVideoFileUrl: false,
  errorVideoFileUrl: null,
  DownloadVideoFileUrl: '',
  videoFileName: '',
};

const getVideoFileHandler = filenameList => {
  //const filenameList =  ["execution.log", "systemDebugLogs.log", "webcam_video.lvi"]
  if (filenameList.length === 0) {
    return;
  }
  const videofileName = filenameList.filter(
    (item: String) => item.split('.')[1] === 'mp4',
    0,
  );
  const reverseList = [...videofileName].reverse();
  return reverseList[0];
};

const slice = createSlice({
  name: 'DownloadLog',
  initialState,
  reducers: {
    loadDownloadLogData: state => {
      state.loading = true;
    },
    DownloadLogDataLoaded: (state, action: PayloadAction<any>) => {
      state.loading = false;
      state.error = null;
      state.videoFileName = getVideoFileHandler(action.payload);
      state.DownloadLogData = action.payload;
    },
    DownloadLogDataError: (state, action: PayloadAction<any>) => {
      state.loading = false;
      state.error = action.payload;
      // state.DownloadLogData = [];
    },
    loadDownloadScreenshotsData: state => {
      state.loadingScreenshots = true;
    },
    DownloadScreenshotsDataLoaded: (state, action: PayloadAction<any>) => {
      state.loadingScreenshots = false;
      state.errorScreenshots = null;
      state.DownloadScreenshotsData = action.payload;
    },
    DownloadScreenshotsDataError: (state, action: PayloadAction<any>) => {
      state.loadingScreenshots = false;
      state.errorScreenshots = action.payload;
      // state.DownloadLogData = [];
    },
    loadDownloadFileUrl: state => {
      state.loadingFileUrl = true;
    },
    getFileUrlLoaded: (state, action: PayloadAction<any>) => {
      state.loadingFileUrl = false;
      state.errorFileUrl = null;
      state.getFileUrlData = action.payload;
    },
    downloadFileUrlLoaded: (state, action: PayloadAction<any>) => {
      state.loadingFileUrl = false;
      state.errorFileUrl = null;
      state.DownloadFileUrl = action.payload;
    },
    downloadFileUrlError: (state, action: PayloadAction<any>) => {
      state.loadingFileUrl = false;
      state.errorFileUrl = action.payload;
      // state.DownloadLogData = [];
    },
    loadDownloadVideoFileUrl: state => {
      state.loadingVideoFileUrl = true;
    },
    downloadVideoFileUrlLoaded: (state, action: PayloadAction<any>) => {
      state.loadingVideoFileUrl = false;
      state.errorVideoFileUrl = null;
      state.DownloadVideoFileUrl = action.payload;
    },
    downloadVideoFileUrlError: (state, action: PayloadAction<any>) => {
      state.loadingVideoFileUrl = false;
      state.errorVideoFileUrl = action.payload;
      // state.DownloadLogData = [];
    },
  },
});

export const { actions: DownloadLogActions } = slice;

export const useDownloadLogSlice = () => {
  useInjectReducer({ key: slice.name, reducer: slice.reducer });
  useInjectSaga({ key: slice.name, saga: DownloadLogSaga });
  return { actions: slice.actions };
};
