import { PayloadAction } from '@reduxjs/toolkit';
import { createSlice } from 'utils/@reduxjs/toolkit';
import { useInjectReducer, useInjectSaga } from 'utils/redux-injectors';
import { TestCaseExperienceSaga } from './saga';
import { TestCaseExperienceState } from './types';

export const initialState: TestCaseExperienceState = {
  loading: false,
  error: null,
  TestCaseExperienceData: [],
};

const slice = createSlice({
  name: 'TestCaseExperience',
  initialState,
  reducers: {
    loadTestCaseExperienceData: state => {
      state.loading = true;
    },
    TestCaseExperienceDataLoaded: (state, action: PayloadAction<any>) => {
      state.loading = false;
      state.error = null;
      state.TestCaseExperienceData = action.payload;
    },
    TestCaseExperienceDataError: (state, action: PayloadAction<any>) => {
      state.loading = false;
      state.error = action.payload;
      //state.TestCaseExperienceData = [];
    },
  },
});

export const { actions: TestCaseExperienceActions } = slice;

export const useTestCaseExperienceSlice = () => {
  useInjectReducer({ key: slice.name, reducer: slice.reducer });
  useInjectSaga({ key: slice.name, saga: TestCaseExperienceSaga });
  return { actions: slice.actions };
};
