// @ts-nocheck
import React from 'react';
import {
  Tabs,
  Tab,
  TabList,
  TabPanels,
  TabPanel,
  Flex,
} from '@chakra-ui/react';
import SubHeaderComp from 'app/components/SubHeaderComp';
import SubContainer from 'app/components/SubContainer';
import MobileDisplay from './MobileDisplay';
import { DisplayScreen } from 'app/pages/DisplayScreen/Loadable';
import ScreentShotViewer from './ScreentshotsViewer';
import ResizablePanel from './ResizablePanel';

function SubTabsComponent({
  listofTabs,
  listofComponents,
  defaultIndex,
  isSubContainer,
  rightContent,
  isBorder,
  tabType,
  deviceType,
  orientation,
  orientationView
}) {
  return (
    <Tabs isLazy={false} defaultIndex={defaultIndex} orientation={orientation}>
      <SubHeaderComp>
        <TabList
          borderBottomWidth={isBorder ? '3px' : '0px'}
          // borderBottomWidth= "3px"
          borderTop={isBorder ? '2px solid #EDEDED' : '0px'}
          borderColor="grey3"
          width="100%"
          fontFamily="Montserrat"
          fontSize="14px"
          fontWeight="500"
          color="grey15"
        >
          {listofTabs &&
            listofTabs.map((tab, index) => (
              <Tab
                key={index}
                paddingLeft="0px"
                paddingRight="0px"
                marginLeft="3px"
                fontWeight="500"
                fontSize="14px"
                marginRight="3px"
                data-sub-tab-id={`tab-button-${tab}`}
                borderBottom="3px solid transparent"
                _focus={{ boxShadow: ' 0 0 0 white' }}
                _selected={{
                  color: 'black',
                  fontWeight: '600',
                  borderBottomColor: 'red',
                }}
              >
                {tab}
              </Tab>
            ))}
        </TabList>
        {rightContent}
      </SubHeaderComp>
      <Flex bg="white" position="relative" minHeight='105vh' >
        {(tabType !== 'lr-interact' || tabType !== 'mobile-interact'  ) ? (
          <DisplayScreen
            orientation={orientationView}
            tabType={tabType}
            deviceType={deviceType}
          ></DisplayScreen>
        ) : (
          <ScreentShotViewer tabType={tabType} ></ScreentShotViewer>
        )}

        <TabPanels 
       // minHeight="105vh" 
        display="flex"
         //bg="white"
          >
          {listofComponents &&
            listofComponents.map((tabcomp, index) => (
              <TabPanel
                key={index}
                display="flex"
                padding="0"
                margin="0"
                width="100%"
              >
                {isSubContainer && <SubContainer>{tabcomp}</SubContainer>}
                {!isSubContainer && tabcomp}
              </TabPanel>
            ))}
        </TabPanels>
        {/* </ResizablePanel> */}
      </Flex>
    </Tabs>
  );
}

export default SubTabsComponent;
