// @ts-nocheck
import { GridItem, Icon, Text } from '@chakra-ui/react';
import InfoComponent from 'app/pages/Appium/components/InfoComponent';
import React from 'react';
import { MdOutlineLaunch } from 'react-icons/md';
import { useLocation, useParams } from 'react-router-dom';
import { REACT_APP_LOGOUT_REDIRECTPAGE } from 'utils/enviroment';
const BASE_URL = REACT_APP_LOGOUT_REDIRECTPAGE;

function GridItemComponent({ heading, value, textTransform ,referenceId}) {
  const location = useLocation()
  const { testId } = useParams();

  const pathWithoutId = location.pathname.replace(`/${testId}`, '');
  const openNewTab = () => {
    window.open(`${BASE_URL}${pathWithoutId}/${referenceId}`, '_blank');
  };

  return (
    <>
      <GridItem w="100%" data-grid-item={`session-data-${heading}`}>
        <Text
          as="span"
          color="grey5"
          data-grid-heading={`session-item-${heading}`}
          display="block"
        >
          {heading}
         {/* {referenceId && <InfoComponent
            labelmessage={`Ref : ${referenceId}`}
          ></InfoComponent>
          
          } */}
         {referenceId &&  <Icon
         cursor="pointer"
          marginLeft="10px"
          color="#7E8299"
          onClick={openNewTab}
          w="14px"
          h="14px"
          as={MdOutlineLaunch}
        />
          
          }
        </Text>
        <Text
          as="b"
          textTransform={textTransform}
          data-grid-value={`session-value-${heading}`}
          display="block"
          color="grey10"
        >
          {value}
        </Text>
      </GridItem>
    </>
  );
}

export default GridItemComponent;
