// @ts-nocheck
import { Flex, Text } from '@chakra-ui/react';
import React, { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import * as am4core from '@amcharts/amcharts4/core';
import * as am4charts from '@amcharts/amcharts4/charts';
import am4themes_animated from '@amcharts/amcharts4/themes/animated';
import am4themes_dataviz from '@amcharts/amcharts4/themes/dataviz';
import {
  selectHarChartData,
  selectHarChartDetailsData,
} from '../slice/selector';

am4core.useTheme(am4themes_dataviz);
am4core.useTheme(am4themes_animated);
am4core.addLicense('ch-custom-attribution');

function HarChartComp({ chartData , id }) {

  
  useEffect(() => {
     //console.log({chartData})
    if(!chartData || !chartData.length) {
      return undefined;
    }
    const harData = chartData.map((item , index)=> {
      const obj = {...item}
      obj.category = `[${index}]${item.category}`; 
      return obj
    })
    //console.log({harData}) 
    var chart = am4core.create(`chartdiv${id}`, am4charts.XYChart);
    chart.data = harData;
    am4core.color('#ff0000');
    am4core.addLicense('ch-custom-attribution');
    //am4core.PointerOrientation
    var categoryAxis = chart.yAxes.push(new am4charts.CategoryAxis());
    categoryAxis.dataFields.category = 'category';
    categoryAxis.renderer.labels.template.truncate = true;
    categoryAxis.renderer.labels.template.warp = true;
    categoryAxis.renderer.labels.template.maxWidth = 200;
    categoryAxis.renderer.labels.template.minWidth = 200;
    categoryAxis.renderer.labels.template.paddingLeft = 0;
    categoryAxis.renderer.labels.template.tooltipText = '{category}';
  
    categoryAxis.renderer.labels.template.adapter.add('text', (text, label) => {
      return `{method} | ${text}`;
    });
   // categoryAxis.renderer.labels.template.adapter.text = '{method}:{category}';
    categoryAxis.tooltip.label.maxWidth = 199;
    categoryAxis.tooltip.label.wrap = true;
    categoryAxis.renderer.labels.template.tooltipX = am4core.percent(100);
    categoryAxis.renderer.labels.template.tooltipY = am4core.percent(50);
    categoryAxis.renderer.cellStartLocation = 0.1;
    categoryAxis.renderer.cellEndLocation = 0.9;
    categoryAxis.renderer.labels.template.rotation = true;
    categoryAxis.renderer.labels.template.fontSize = 12;
    categoryAxis.renderer.labels.template.fill = am4core.color('#707070');
    categoryAxis.renderer.grid.template.location = 0;
    categoryAxis.renderer.minGridDistance = 20;
    categoryAxis.renderer.maxGridDistance = 22;
    categoryAxis.renderer.ticks.template.disabled = false;
    categoryAxis.renderer.ticks.template.strokeOpacity = 0;
    categoryAxis.renderer.grid.template.strokeWidth = 1;
    categoryAxis.renderer.inversed = true;
    var valueAxis = chart.xAxes.push(new am4charts.ValueAxis());
    valueAxis.tooltip.disabled = true;
    valueAxis.calculateTotals = true;
    valueAxis.renderer.grid.template.strokeWidth = 0;
    valueAxis.renderer.opposite = false;
    valueAxis.renderer.labels.template.disabled = true;

    //  valueAxis.tooltip.disabled = true;
    let block = 'blocked' === -1 ? '-1' : 'blocked';

    let tooltipHTML = `<center><strong><h3 style="text-transform : capitalize" >Request Time{categoryX}</h3></strong></center>
  <hr />
  <table style="padding : 5px" >
  <tr>
    <th align="left">Blocked time</th>
    <td> {blockedTooltip}</td>
  </tr>
  <tr>
    <th align="left">Connection time</th>
    <td> {connectTooltip}</td>
  </tr>
  <tr>
    <th align="left">Send time</th>
    <td>{sendTooltip}</td>
  </tr>
  <tr>
    <th align="left">Wait time</th>
    <td>{waitTooltip}</td>
  </tr>
  <tr>
    <th align="left">Receive time</th>
    <td>{reciveTooltip}</td>
  </tr>
  <tr>
    <th align="left">SSL time</th>
    <td>{sslTooltip}</td>
  </tr>
  <tr>
    <th align="left">DNS time</th>
    <td>{sslTooltip}</td>
  </tr>
  </table>
  <hr />`;

    // create series
    function createSeries(open, close, names, showSum, color, values ,opacity ) {
      var series = chart.series.push(new am4charts.ColumnSeries());

      series.columns.template.tooltipHTML = tooltipHTML;
      series.fillOpacity = opacity ? 0.1 : 1; 
      series.tooltip.getFillFromObject = false;
      series.tooltip.background.fill = am4core.color('#6CB2D8');
      series.tooltip.label.fill = am4core.color('#ffffff');
      series.chart.tooltip.label.fontSize = 12;
      series.chart.tooltip.label.textTransform = 'capitalize';

      series.dataFields.valueX = close;
      series.dataFields.openValueX = open;
      series.dataFields.categoryY = 'category';
      series.clustered = false;
      series.strokeWidth = 0;
      series.fill = am4core.color(color);

      if (showSum) {
        const sumBullet = series.bullets.push(new am4charts.LabelBullet());
        sumBullet.label.text = '{totalTime}';
        sumBullet.label.truncate = false;
        sumBullet.label.fontSize = 10;
        sumBullet.label.horizontalCenter = 'left';
        sumBullet.verticalCenter = 'bottom';
        // sumBullet.locationX = 2;
        // sumBullet.dy = 0;
        sumBullet.dx = 5;
        // sumBullet.label.adapter.add('text', function (text, target) {
        //   var val = Math.abs(
        //     target.dataItem.dataContext.receiveClose -
        //       target.dataItem.dataContext.blockedOpen,
        //   ).toFixed(1);
        //   if (val < 1000) {
        //     return val + 'ms';
        //   } else if (val >= 1000 && val < 60000) {
        //     return (val / 1000).toFixed(1) + 's';
        //   }
        //   return (val / 3600).toFixed(1) + 'm';
        // });
        sumBullet.label.adapter.add('text', function (text, target) {
          var val = Math.abs(
            target.dataItem.dataContext.totalTime
          ).toFixed(1);
          if (val < 1000) {
            return val + 'ms';
          } else if (val >= 1000 && val < 60000) {
            return (val / 1000).toFixed(1) + 's';
          }
          return (val / 3600).toFixed(1) + 'm';
        });
      }
    }
    // chart.scrollbarX = new am4core.Scrollbar();
    // chart.cursor = new am4charts.XYCursor();
    // chart.cursor.maxTooltipDistance = -1;

    createSeries('startTime', 'beginingTime', '', false, '#ffffff' , true , true);
    createSeries('blockedOpen', 'blockedClose', 'block', false, '#EC9332' , false);
    createSeries('dnsOpen', 'dnsClose', 'dns', false, '#8e6e4b' , false);
    createSeries('sslOpen', 'sslClose', 'ssl', false, '#B14D93' , false);
    createSeries('connectOpen', 'connectClose', 'connect', false, '#2F7CCB', false);
    createSeries('sendOpen', 'sendClose', 'send', false, '#5FA730' , false);
    createSeries('waitOpen', 'waitClose', 'wait', false, '#F9D1E2' , false);
    createSeries('receiveOpen', 'receiveClose', 'receive', true, '#C44129' , false);
    //    setData(harData);
    return () => {
      chart.dispose();
    }
  }, [chartData]);

 
if(!chartData || !chartData.length) {
  return (<>No Content</>)
}

  return (
    <>
  
        <Flex
          position="absolute"
          zIndex="1"
          flexDirection="column"
          marginTop="10px"
          //   height={
          //           harData.length <= 8
          //       ? harData.length * 37 + 'px'
          //       : harData.length * 28 + 'px'
          //   }
          height={
            chartData.length == 1
              ? chartData.length * 55 + 'px'
              : chartData.length <= 3
              ? chartData.length * 45 + 'px'
              : chartData.length <= 8
              ? chartData.length * 37 + 'px'
              : chartData.length * 28 + 'px'
          }
          background="transparent"
          width="100%"
          bg="transparent"
          paddingRight="18px"
          //   position = 'relative'
          //boxShadow="  0px 0px 6px #00000029 inset"
          // borderLeft="1px solid #00000029 "
          // borderRight="1px solid #00000029 "
          textTransform="lowercase"
          id={`chartdiv${id}`}
        ></Flex>
      
    </>
  );
}

export default HarChartComp;
