// @ts-nocheck
import React from 'react';
import { Grid, Text, GridItem } from '@chakra-ui/react';
import NoContent from 'app/components/NoContent';
import { dateTimeConverter } from 'utils/gernricfunction';
import GridItemComponent from './GridItemComponent';

function SessionidTable({ testSessionData, tabType }) {
  // console.log(testSessionData)
  return (
    <>
      {Object.keys(testSessionData).length !== 0 ? (
        <Grid
          fontFamily="Montserrat"
          fontWeight="500"
          backgroundColor="white"
          padding="4"
          alignContent="flex-start"
          templateColumns="repeat(6, 1fr)"
          gridRowGap="1"
          date-grid-table="session-data-table"
          gap={3}
        >
       
          <GridItemComponent
            textTransform="none"
            heading={
              testSessionData.uuid.testId === '' ? 'Session ID' : 'Test ID'
            }
            value={
              testSessionData.uuid.testId || testSessionData.uuid.sessionId
            }
            referenceId={testSessionData.uuid?.referenceId}
          ></GridItemComponent>
    
          <GridItemComponent
            heading="Start Date & Time"
            value={dateTimeConverter(testSessionData.testStartTime)}
          ></GridItemComponent>
          <GridItemComponent
            heading="End Date & Time"
            value={dateTimeConverter(testSessionData.testEndTime)}
          ></GridItemComponent>
          {tabType !== 'lr-interact' && (
            <GridItemComponent
              heading="Project"
              value={testSessionData.projectName}
            ></GridItemComponent>
          )}

          {tabType !== 'lr-interact' && (
            <GridItemComponent
              heading="Build"
              value={testSessionData.appVersion}
            ></GridItemComponent>
          )}
          {tabType !== 'lr-interact' && (
            <GridItemComponent
              heading="Script"
              value={testSessionData.scriptName}
            ></GridItemComponent>
          )}
          <GridItemComponent
            heading="Device"
            value={testSessionData.deviceName}
          ></GridItemComponent>
             {tabType !== 'lr-interact' && (
            <GridItemComponent
              heading="Device Serial"
              value={testSessionData.deviceSerial}
            ></GridItemComponent>
          )}
             {(tabType !== 'lr-interact' && testSessionData.extra.hasOwnProperty('trayName')) && (
            <GridItemComponent
              heading="Tray"
              value={testSessionData.extra.trayName}
            ></GridItemComponent>
          )}
             {tabType !== 'lr-interact' && (
          <GridItemComponent
            heading="OS"
            value={testSessionData.deviceOSVersion}
          ></GridItemComponent>)}
          <GridItemComponent
            heading="Location"
            value={testSessionData.deviceLocation}
          ></GridItemComponent>
          {tabType !== 'lr-interact' && (
            <GridItemComponent
              heading="Network"
              value={testSessionData.deviceNetwork}
            ></GridItemComponent>
          )}
          {tabType !== 'lr-interact' && (
            <GridItemComponent
              heading="Operator"
              value={testSessionData.operator}
            ></GridItemComponent>
          )}
        </Grid>
      ) : (
        <NoContent message="No Content Available"></NoContent>
      )}
    </>
  );
}

export default SessionidTable;
