// @ts-nocheck
import { Flex, Text } from '@chakra-ui/react';
import ChartLayout from 'app/components/ChartLayout';
import NoContent from 'app/components/NoContent';
import SliderAxis from 'app/pages/AppProfiling/components/SliderAxis';
import React, { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import HarChartStatus from '../Components/HarChartStatus';
import RadioButton from '../Components/RadioButton';
import SliderAxisHar from '../Components/SliderAxisHar';
import {
  selectHarChartData,
  selectHarChartDetail,
  selectHarChartDetailData,
  selectHarChartDetailsData,
} from '../slice/selector';
import HarChart from './HarChart';
import Statistics from './Statistics';

const getAssetTypeArray = dataArray => {
  //console.log({dataArray});
  //const element = dataArray
  if(dataArray.length === 0) {
    return []
  }
  const assetType = [
    ...new Set(
      dataArray.map(item =>
        item.AssetType
      ),
    ),
  ];
//console.log({assetType})
  return assetType.filter(str => str && str.trim().length > 0);
};

function HarFilter({ startTimeIST, endTimeIST, duration }) {
  const [selectAssetType, setSelectAsset] = useState([]);
  const [radioFilter, setRadioFilter] = useState('');
  const [radioFilterInputWaitTime, setRadioFilterInputWaitTime] = useState(200);
  const [radioFilterInputBlockedTime, setRadioFilterInputBlockedTime] =
    useState(200);
  const [radioFilterInputUrl, setRadioFilterInputUrl] = useState('');
  const [radioFilterSelectAsset, setRadioFilterSelectAsset] = useState('');
  const harEntiesData = useSelector(selectHarChartData);
  const [filterData, setFilterData] = useState(harEntiesData);
  const HarDetailData = useSelector(selectHarChartDetailData);
  useEffect(() => {
    ApplyFilterHandler();
    // console.log(HarDetailData)

    setSelectAsset(getAssetTypeArray(harEntiesData));
  }, [harEntiesData, HarDetailData]);
  // console.log(selectAssetType)
  useEffect(() => {
    if (radioFilter === '') {
      ApplyFilterHandler();
    }
  }, [radioFilter]);

  const ApplyFilterHandler = () => {
    if (radioFilter === '') {
      setFilterData(harEntiesData);
      //        console.log(harEntiesData)
    } else if (radioFilter === 'httpError') {
      const data = harEntiesData.filter(item => item.status > 399);
      setFilterData(data);
      //         console.log(data)
    } else if (radioFilter === 'waitTime') {
      const data = harEntiesData.filter(
        item => item.wait > radioFilterInputWaitTime,
      );
      setFilterData(data);
      //  console.log(data)
    } else if (radioFilter === 'blockTime') {
      const data = harEntiesData.filter(
        item => item.blocked > radioFilterInputBlockedTime,
      );
      setFilterData(data);
      //  console.log(data)
    } else if (radioFilter === 'url') {
      const data = harEntiesData.filter(item => {
        if (radioFilterInputUrl === '') {
          return item;
        } else {
          return item.category.toLowerCase().includes(radioFilterInputUrl);
        }
      });
      setFilterData(data);
      //  console.log(data)
    } else if (radioFilter === 'assetType' ) {
      const data = harEntiesData.filter(item =>
        //item.AssetType.includes(radioFilterSelectAsset),
         item.AssetType === radioFilterSelectAsset
        );
      setFilterData(data);
    }
    //console.log('apply running');
   
  };
 //console.log(filterData);
  const radioResetHandler = () => {
    setRadioFilter('');
    setRadioFilterInputWaitTime(200);
    setRadioFilterInputBlockedTime(200);
    setRadioFilterInputUrl('');
    setRadioFilterSelectAsset('');

    ApplyFilterHandler();
  };
  //  setTimeout(ApplyFilterHandler, 2000)

  //console.log({filterData});
  return (
    <ChartLayout height="608px" leftalign="206px">
      {/*

<TestCasesChart></TestCasesChart> */}
      <Flex
        //  zIndex="0"
        fontFamily="Montserrat"
        justifySelf="center"
        height="70px"
        // backgroundColor="transparent"
        backgroundColor="transparent"
        color="black"
        border="1px solid #00000029"
        borderRadius="5px 5px"
        //  boxShadow="0px  3px 6px #00000029 inset"
      >
       
        <RadioButton
          radioFilter={radioFilter}
          ApplyFilterHandler={ApplyFilterHandler}
          radioFilterInputWaitTime={radioFilterInputWaitTime}
          setRadioFilterInputWaitTime={setRadioFilterInputWaitTime}
          radioFilterInputBlockedTime={radioFilterInputBlockedTime}
          setRadioFilterInputBlockedTime={setRadioFilterInputBlockedTime}
          radioFilterInputUrl={radioFilterInputUrl}
          setRadioFilterInputUrl={setRadioFilterInputUrl}
          radioFilterSelectAsset={radioFilterSelectAsset}
          setRadioFilterSelectAsset={setRadioFilterSelectAsset}
          radioResetHandler={radioResetHandler}
          setRadioFilter={setRadioFilter}
          selectAssetType={selectAssetType}
        ></RadioButton>
      </Flex>
      <SliderAxisHar
        isLeft={true}
        startTime={startTimeIST}
        endTime={endTimeIST}
        duration={duration}
      ></SliderAxisHar>
      <Flex
        height="100%"
        //position="relative"
        //   boxShadow="0px 3px 6px #00000029 inset"
        border="1px solid #00000029"
        backgroundColor="transparent"
        //height="500px"
        borderRadius="5px"
        overflowX="hidden"
        overflowY="auto"
        css={{
          '&::-webkit-scrollbar': {
            width: '8px',
            //height: '30px',
            border: '5px solid #F3F6F9',
          },
          '&::-webkit-scrollbar-track': {
            boxShadow: 'inset 0 0 6px white',
          },
          '&::-webkit-scrollbar-thumb': {
            backgroundColor: '#C1C1C1',
            borderRadius: '5px',
            boreder: '2px solid',
          },
        }}
      >
        {HarDetailData.onContentLoad - HarDetailData.onLoadTime > 0 ? (
          <NoContent message="No Content Available"></NoContent>
        ) : (
          <HarChart harData={filterData}></HarChart>
        )}
      </Flex>
      <HarChartStatus NoofRequest={filterData.length}></HarChartStatus>
    </ChartLayout>
  );
}

export default HarFilter;
