import { call, put, takeLatest, delay } from 'redux-saga/effects';
import { request } from 'utils/request';
import { TestCaseExperienceActions as actions } from '.';
import { TestCaseExperienceData } from 'types/TestCaseExperience';
import { TestCaseExperienceErrorType } from './types';
import GetIdToken from 'Authentication/configuration/GetIdToken';
import { REACT_APP_TEST_ANALYTICS_BASE_URL } from 'utils/enviroment';

const BASE_URL = REACT_APP_TEST_ANALYTICS_BASE_URL;

/**ss
 * Github repos request/response handler
 */
export function* getTestCaseExperienceData(id) {
  const idToken = GetIdToken();
  const requestURL = `${BASE_URL}/tests/${id.payload}/app/kpi/experience`;
  const config = {
    method: 'GET',
    headers: {
      'Content-type': 'application/json',
      Authorization: `Bearer ${idToken}`,
    },
  };
  try {
    // Call our request helper (see 'utils/request')
    const TestCaseExperienceData = yield call(request, requestURL, config);
    //   console.log(TestCaseExperienceData)
    if (Object.keys(TestCaseExperienceData.body).length !== 0) {
      yield put(
        actions.TestCaseExperienceDataLoaded(
          TestCaseExperienceData.body.experience,
        ),
      );
    } else {
      yield put(
        actions.TestCaseExperienceDataError(
          TestCaseExperienceErrorType.RESPONSE_ERROR,
        ),
      );
    }
  } catch (err) {
    /*
    if (err.response?.status === 404) {
      yield put(actions.repoError(RepoErrorType.USER_NOT_FOUND));
    } else if (err.message === 'Failed to fetch') {
      yield put(actions.repoError(RepoErrorType.GITHUB_RATE_LIMIT));
    } else {
    */
    yield put(
      actions.TestCaseExperienceDataError(
        TestCaseExperienceErrorType.RESPONSE_ERROR,
      ),
    );
  }
}

/**
 * Root saga manages watcher lifecycle
 */
export function* TestCaseExperienceSaga() {
  // Watches for loadRepos actions and calls getRepos when one comes in.
  // By using `takeLatest` only the result of the latest API call is applied.
  // It returns task descriptor (just like fork) so we can continue execution
  // It will be cancelled automatically on component unmount
  yield takeLatest(
    actions.loadTestCaseExperienceData.type,
    getTestCaseExperienceData,
  );
}
