// @ts-nocheck
import { Flex } from '@chakra-ui/react';
import LoadingComponent from 'app/components/LoadingComponent';
import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useParams } from 'react-router-dom';
import TestCasesTable from '../TestCases/components/TestCasesTable';
import { useTestCaseExperienceSlice } from './slice';
import {
  selectError,
  selectLoading,
  selectTestCaseExperienceData,
} from './slice/selectors';
const ExperienceKPIsTableHeading = [
  'Journey Name',
  'KPI Name',
  //'End Time',
  'Value',
];
export function TestCaseExperience() {
  const { testId } = useParams();
  const { actions } = useTestCaseExperienceSlice();
  const dispatch = useDispatch();
  const error = useSelector(selectError);
  const TestCaseExperienceData = useSelector(selectTestCaseExperienceData);
  const isLoading = useSelector(selectLoading);

  useEffect(() => {
    dispatch(actions.TestCaseExperienceDataLoaded([]));
    dispatch(actions.loadTestCaseExperienceData(testId));
  }, []);

  //   console.log(TestCaseExperienceData)

  return (
    <Flex width="100%">
      {isLoading ? (
        <LoadingComponent></LoadingComponent>
      ) : (
        <TestCasesTable
          tableHeader={ExperienceKPIsTableHeading}
          TestCaseSummaryTableData={TestCaseExperienceData}
          title="Experience KPIs"
          error={error}
        ></TestCasesTable>
      )}
    </Flex>
  );
}

export default TestCaseExperience;
