import { createSelector } from '@reduxjs/toolkit';

import { RootState } from 'types';
import { initialState } from '.';

// First select the relevant part from the state
const selectSlice = (state: RootState) => state.DisplayScreen || initialState;

export const selectDisplayScreenData = createSelector(
  [selectSlice],
  state => state.DisplayScreenData,
);

export const selectLoading = createSelector(
  [selectSlice],
  state => state.loading,
);

export const selectError = createSelector([selectSlice], state => state.error);

export const selectScreenshotUrl = createSelector(
  [selectSlice],
  state => state.screenShotUrl,
);

